import "./dependencies/node-extensions";
import "./dependencies/path-data";
import History from "./tools/history";
import Shape from "./tools/shape";
import Matrix from "./dependencies/matrix";
import layout from "./support/layout";
import Text from "./tools/text";
import Pan from "./tools/pan";
import Spline from "./tools/spline";
import Freehand from "./tools/freehand";
import View from "./tools/view";
import Hover from "./support/hover";
import Clipboard from "./support/clipboard";
import Select from "./tools/select";
import Resize from "./tools/resize";
import Contextmenu from "./tools/contextmenu";
import Scale from "./tools/scale";
import Ruler from "./support/ruler";

interface Options {
  fill?: string;
  stroke?: string;
  strokeWidth?: number;
  fillOpacity?: number;
  textFill?: string;
  textContent?: string;
  fontSize?: number;
  grippieSize?: number;
  outerCircleSize?: number;
  innerCircleSize?: number;
  bezierControlSize?: number;
  secondaryGgrippieSize?: number;
}

export class Stage {
  matrix = { a: 1, b: 0, c: 0, d: 1, e: 0, f: 0 };
  mmUnit = 2.83464;
  CAN_CEHCKED_NODE: Array<string> = [
    "rect",
    "circle",
    "image",
    "text",
    "ellipse",
    "line",
    "g",
    "path",
    "polygon",
    "polyline",
  ];
  currentType?: Symbol;
  options: Options = {
    fill: "#d8d8d8",
    stroke: "#000000",
    strokeWidth: 1,
    fillOpacity: 1,
    textFill: "#000000",
    textContent: "Enter your text here",
    fontSize: 26,
    grippieSize: 11,
    outerCircleSize: 5,
    innerCircleSize: 1.5,
    bezierControlSize: 2.8,
    secondaryGgrippieSize: 4,
  };
  layer: any;
  stage?: HTMLElement;
  canvas?: SVGElement;
  svgRoot?: SVGElement;
  boardView?: SVGRectElement;
  select?: Select;
  resize?: Resize;
  hover?: Hover;
  history?: History;
  shape?: Shape;
  pan?: Pan;
  spline?: Spline;
  view?: View;
  freehand?: Freehand;
  text?: Text;
  clipboard?: Clipboard;
  contextmenu?: Contextmenu;
  scale?: Scale;
  ruler?: Ruler;
  #TOOL = {
    SELECT: Symbol(),
    ASELECT: Symbol(),
    RECT: Symbol(),
    ELLIPSE: Symbol(),
    TRIANGLE: Symbol(),
    LINE: Symbol(),
    OTHER_SHAPE: Symbol(),
    SPLINE: Symbol(),
    FREEHAND: Symbol(),
    TEXT: Symbol(),
    PAN: Symbol(),
    VIEW: Symbol(),
  };
  #WATCH_ACTIONS = {
    SELECTED: Symbol(),
    FRAME_RECT: Symbol(),
    HISTORY: Symbol(),
    VIEW: Symbol(),
    UPDATED: Symbol(),
    TOOL: Symbol(),
    SCALE: Symbol(),
    LAYER: Symbol(),
    TEXT: Symbol(),
    DRAW: Symbol(),
    DRAG_BEFORE: Symbol(),
  };
  #listenSvgRoot?: MutationObserver;
  #listenScale?: MutationObserver;
  #selectCallback = new Map();
  #rectCallback = new Map();
  #updatedCallback = new Map();
  #toolCallback = new Map();
  #scaleCallback = new Map();
  #layerCallback = new Map();
  #textCallback = new Map();
  #drawCallback = new Map();
  #viewCallback = new Map();
  #dragBeforeCallback = new Map();
  watch = (action: Symbol, func: Function) => {
    if (this.actions().SELECTED === action) {
      this.#selectCallback?.set(func, func);
    } else if (this.actions().FRAME_RECT === action) {
      this.#rectCallback?.set(func, func);
    } else if (this.actions().UPDATED === action) {
      this.#updatedCallback?.set(func, func);
    } else if (this.actions().TOOL === action) {
      this.#toolCallback?.set(func, func);
    } else if (this.actions().SCALE === action) {
      this.#scaleCallback?.set(func, func);
    } else if (this.actions().LAYER === action) {
      this.#layerCallback?.set(func, func);
    } else if (this.actions().TEXT === action) {
      this.#textCallback?.set(func, func);
    } else if (this.actions().DRAW === action) {
      this.#drawCallback?.set(func, func);
    } else if (this.actions().VIEW === action) {
      this.#viewCallback.set(func, func);
    } else if (this.actions().DRAG_BEFORE === action) {
      this.#dragBeforeCallback.set(func, func);
    }
  };
  actions = () => {
    return this.#WATCH_ACTIONS;
  };
  tools = () => {
    return this.#TOOL;
  };
  setViewBox = (width: number, height: number) => {
    const stage = this.stage?.querySelector("svg") as SVGSVGElement;
    stage.setAttribute("width", width.toFixed(9));
    stage.setAttribute("height", height.toFixed(9));
    stage.setAttribute("viewBox", `0  0 ${width} ${height}`);
  };
  init = (
    container: HTMLElement,
    width = 600,
    height = 600,
    options?: Options
  ) => {
    if (!container) throw new Error("set content please.");
    this.options = Object.assign(this.options, options);
    this.stage = container;
    const stage = createNode("svg") as SVGSVGElement;
    stage.style.userSelect = "none";
    stage.setAttribute("width", width.toString());
    stage.setAttribute("height", height.toString());
    stage.setAttribute("viewBox", `0  0 ${width} ${height}`);
    stage.style.background = "#f0f0f0";
    stage.innerHTML = layout(this.matrix, this.options.grippieSize || 11);
    this.stage?.append(stage);
    this.setViewSize(window.innerHeight / 1.5, window.innerHeight / 1.5);
    this.#initElement();
    this.#registerDependencies();
    this.switchLayer();
    this.#addEventListener();
  };
  #initElement = () => {
    this.svgRoot = document.querySelector("#lux-svg-root") as SVGElement;
    this.canvas = document.querySelector('[uid="canvas"]') as SVGElement;
    this.boardView = document.querySelector(
      "#background-outlines rect"
    ) as SVGRectElement;
  };
  #registerDependencies = () => {
    this.#useShape();
    this.#useSelect();
    this.resize = new Resize(this);
    this.history = new History(this);
    this.#useView();
    this.spline = new Spline(this);
    this.freehand = new Freehand(this);
    this.pan = new Pan(this);
    this.hover = new Hover(this);
    this.clipboard = new Clipboard(this);
    this.contextmenu = new Contextmenu();
    this.scale = new Scale(this);
    this.ruler = new Ruler(this);
    this.#useText();
  };
  #useShape = () => {
    this.shape = new Shape(this, {
      start: () => {
        this.#drawCallback?.forEach((func: Function) => {
          func("start");
        });
      },
      done: (node: SVGElement) => {
        node.setAttribute("data-fill", "0:0:0:0");
        node.setAttribute("data-stroke", "0:0:0:100");
        this.#drawCallback?.forEach((func: Function) => {
          func("end", node);
        });
      },
    });
  };
  #useView = () => {
    this.view = new View(this);
    this.view.watch(() => {
      this.#viewCallback.forEach((func: Function) => {
        func();
      });
    });
  };
  #useSelect = () => {
    this.select = new Select(this);
    this.select?.watch(this.select?.actions().SELECTED, () => {
      this.#selectCallback.forEach((func: Function) => {
        func();
      });
    });
    this.select?.watch(this.select?.actions().FRAME_RECT, () => {
      this.#rectCallback.forEach((func: Function) => {
        func();
      });
    });
    this.select?.watch(this.select?.actions().DRAG_BEFORE, () => {
      this.#dragBeforeCallback.forEach((func: Function) => {
        func();
      });
    });
  };
  #useText = () => {
    this.text = new Text(this);
    this.text.watch(this.text.actions().ADD, (text: SVGTextElement) => {
      this.focusNode(text);
      this.#textCallback?.forEach((func: Function) => {
        func(text, "add");
      });
    });

    this.text.watch(this.text.actions().EDIT, (text: SVGTextElement) => {
      this.focusNode(text);
      this.select?.resetFrame(true);
      this.#textCallback?.forEach((func: Function) => {
        func(text, "edit");
      });
    });

    this.text.watch(this.text.actions().QUIT, (text: SVGTextElement) => {
      this.#textCallback?.forEach((func: Function) => {
        func(text, "quit");
      });
    });
  };
  #addEventListener = () => {
    this.#listenerScale();
    this.#listenerSvgRoot();
  };
  #listenerScale = () => {
    const transformHud = document.querySelector("#transform-hud");
    const grippie = transformHud?.querySelectorAll(
      ".grippie"
    ) as NodeListOf<Element>;
    const oG = document.querySelector('[uid="origin-grippie"]') as SVGElement;

    this.#listenScale = new MutationObserver(() => {
      const gSize = this.options.grippieSize || 11;
      const { a } = this.matrix;
      for (let i = 0; i < grippie.length; i++) {
        const node = grippie[i];
        node.setAttribute("width", (gSize / a).toFixed(9));
        node.setAttribute("height", (gSize / a).toFixed(9));
      }
      oG.setAttribute("width", (gSize / a).toFixed(9));
      oG.setAttribute("height", (gSize / a).toFixed(9));

      this.matrix = this.canvas?.getMatrix();
      this.#scaleCallback?.forEach((func: Function) => {
        func(this.matrix);
      });

      if (this.currentType === this.tools().SPLINE) {
        const grippies = document.querySelectorAll(".secondary-node-grippie");
        const radius =
          (this.options.secondaryGgrippieSize || 4) / this.matrix.a;
        grippies?.forEach((node: Element) => {
          node.setAttribute("r", radius.toFixed(9));
        });

        const bezierC1 = document.querySelector(
          "[uid='control-point-1-grippie']"
        );
        const bezierC2 = document.querySelector(
          "[uid='control-point-2-grippie']"
        );
        const radius1 = (this.options.bezierControlSize || 2.8) / this.matrix.a;
        bezierC1?.setAttribute("r", radius1.toFixed(9));
        bezierC2?.setAttribute("r", radius1.toFixed(9));
      }

      if (
        this.currentType === this.tools().FREEHAND ||
        this.currentType === this.tools().SPLINE
      ) {
        const outerSize = (this.options.outerCircleSize || 5) / this.matrix.a;
        const innerSize = (this.options.innerCircleSize || 1.5) / this.matrix.a;
        const outerCircles = document.querySelectorAll(".outer-circle");
        const innerCircles = document.querySelectorAll(".inner-circle");
        outerCircles?.forEach((node: Element) => {
          node.setAttribute("r", outerSize.toFixed(9));
        });
        innerCircles?.forEach((node: Element) => {
          node.setAttribute("r", innerSize.toFixed(9));
        });
      }
    });

    this.#listenScale.observe(this.canvas as any, {
      attributes: true,
      attributeFilter: ["transform"],
    });
  };
  #listenerSvgRoot = () => {
    this.#listenSvgRoot = new MutationObserver((record) => {
      record.forEach((item) => {
        item.addedNodes.forEach((node: any) => {
          if (node.nodeType === 3) return;
          if (!this.CAN_CEHCKED_NODE.includes(node?.localName)) {
            node?.setAttribute("pointer-events", "none");
          }

          if (node instanceof SVGPathElement) {
            node?.toUpperCase();
          } else {
            const paths = node.querySelectorAll("path");
            paths.forEach((path: SVGPathElement) => {
              path.toUpperCase();
            });
          }

          if (node instanceof SVGTextElement) {
            node.style.whiteSpace = "pre";
          } else {
            const texts = node?.querySelectorAll("text");
            texts.forEach((text: SVGTextElement) => {
              text.style.whiteSpace = "pre";
            });
          }
        });
      });
      this.#updatedCallback?.forEach((func: Function) => {
        func(record);
      });
    });

    this.#listenSvgRoot.observe(this.svgRoot as any, {
      childList: true,
      subtree: true,
    });
  };
  tool = (currentType: Symbol, ignore?: boolean) => {
    if (
      (!currentType ||
        currentType === this.currentType ||
        this.select?.isDragging() ||
        this.select?.isDrawingRubber() ||
        this.resize?.isResizing() ||
        this.shape?.isDrawing()) &&
      !ignore
    )
      return;

    this.currentType = currentType;

    if (this.currentType !== this.tools().PAN) {
      this.layerDisabled();
    }

    this.select?.disabled(true);
    this.shape?.disabled(true);
    this.view?.disabled(true);
    this.spline?.disabled(true);
    this.freehand?.disabled(true);
    this.pan?.disabled(true);
    this.text?.disabled(true);
    this.text?.quit();

    const stage = this.stage as HTMLElement;

    if (
      this.currentType !== this.tools().SELECT &&
      this.currentType !== this.tools().ASELECT &&
      this.currentType !== this.tools().TEXT
    ) {
      this.select?.toggleFrame(true);
    }

    if (this.currentType === this.tools().SELECT) {
      this.select?.resetFrame(true);
      this.select?.disabled(false);
      stage.style.cursor = `auto`;
      this.select?.get().forEach((item: any) => {
        if (!this.layer.contains(item.node)) return this.select?.reset();
      });
    } else if (this.currentType === this.tools().ASELECT) {
      this.select?.resetFrame(true);
      this.select?.disabled(false);
      stage.style.cursor = `auto`;
      this.select?.get().forEach((item: any) => {
        if (item.node.localName === "g") return this.select?.reset();
      });
    } else if (this.currentType === this.tools().PAN) {
      stage.style.cursor = `grab`;
      this.pan?.disabled(false);
    } else if (this.currentType === this.tools().TEXT) {
      stage.style.cursor = "text";
      this.text?.disabled(false);
      if (
        this.select?.size() === 1 &&
        this.select?.first().node.localName === "text"
      ) {
        this.text?.edit(null, this.select?.first().node, true);
      }
    } else if (this.currentType === this.tools().SPLINE) {
      stage.style.cursor = "crosshair";
      this.spline?.disabled(false);
    } else if (this.currentType === this.tools().FREEHAND) {
      stage.style.cursor = "crosshair";
      this.freehand?.disabled(false);
    } else if (this.currentType === this.tools().VIEW) {
      stage.style.cursor = "auto";
      this.view?.disabled(false);
    } else if (
      this.currentType === this.tools().RECT ||
      this.currentType === this.tools().ELLIPSE ||
      this.currentType === this.tools().TRIANGLE ||
      this.currentType === this.tools().LINE ||
      this.currentType === this.tools().OTHER_SHAPE
    ) {
      stage.style.cursor = "crosshair";
      this.shape?.disabled(false);
    }
    this.#toolCallback.forEach((func: Function) => {
      func(this.currentType);
    });
  };
  setViewSize = (width: number, height: number, x?: number, y?: number) => {
    this.boardView?.setAttribute("width", width.toString());
    this.boardView?.setAttribute("height", height.toString());
    if (x) this.boardView?.setAttribute("x", x.toString());
    if (y) this.boardView?.setAttribute("y", y.toString());
  };
  getViewSize = () => {
    return {
      width: Number(this.boardView?.getAttribute("width")) || 0,
      height: Number(this.boardView?.getAttribute("height")) || 0,
      x: Number(this.boardView?.getAttribute("x")) || 0,
      y: Number(this.boardView?.getAttribute("y")) || 0,
    };
  };
  setBoardSize = (scale: number, x?: number, y?: number) => {
    this.matrix.a = scale || this.matrix.a;
    this.matrix.d = scale || this.matrix.a;
    this.matrix.e = x || this.matrix.e;
    this.matrix.f = y || this.matrix.f;
    const m = this.matrix;
    this.canvas?.setAttribute(
      "transform",
      `matrix(${m.a},${m.b},${m.c},${m.d},${m.e},${m.f})`
    );
    this.select?.resetFrame(false);
  };
  nodesCenterScale = (sx: number, sy: number, historyText = "Center Scale") => {
    if (this.select?.size() === 0) return;
    sx = sx / 100;
    sy = sy / 100;

    this.history?.add(this.history.actions().SCALE, historyText);
    const { width, height } = this.select
      ?.frame()
      ?.getBoundingClientRect() as DOMRect;
    const x = width * sx - width;
    const y = height * sy - height;
    const dx = Math.abs(x) / 2;
    const dy = Math.abs(y) / 2;

    this.resize?.resize({
      dataX: x,
      dataY: y,
      pos: "bottom-right",
    });

    this.nodesMove("right", dx, false);
    this.nodesMove("down", dy, false);
    this.select?.resetFrame(true);
  };

  paragraphSplit = () => {
    if (this.select?.size() === 0) throw new Error("not checked node.");

    this.select?.get().forEach((item: any) => {
      const node = item.node;
      if (node.localName !== "text") return;
      const text = node;
      const fontSize = text.getAttribute("font-size");
      const fontFamily = text.getAttribute("font-family");
      const ts = text.querySelector("tspan");
      if (!ts) throw new Error("not has tspan.");
      text.childNodes.forEach((ChildNode: Element) => {
        const t = createNode("text");
        t.setAttribute("font-size", fontSize);
        t.setAttribute("font-family", fontFamily);
        t.append(document.createTextNode(ChildNode.textContent || ""));
        text.parentNode?.append(t);
      });
    });

    // const lh = ts.getAttribute("dy");
    // const ct = text.cloneNode(true) as Element;
    // text.parentNode?.append(ct);
    // const { height } = ct.getBoundingClientRect() as DOMRect;
    // ct.remove();
    // const txArr = text.querySelectorAll("tspan");
    // txArr.forEach((t: Element) => {});
  };

  paragraphSpacing = (direction: "", distance = 0) => {
    if (!direction) throw new Error("not set direction.");
    if (this.select?.size() === 0) throw new Error("not checked node.");
    this.history?.add(this.history.actions().EDIT, "段落间距");
    let line = 0;
    const scale = this.canvas?.getScale().sx;
    let i = 0;
    const sArr = [...(this.select?.get().values() as any)];

    sArr.sort((a, b) => {
      return direction === "v"
        ? a.node.getBoundingClientRect().y - b.node.getBoundingClientRect().y
        : a.node.getBoundingClientRect().x - b.node.getBoundingClientRect().x;
    });

    sArr.forEach((item: any) => {
      if (i > 0) {
        const node = item.node as SVGElement;
        const { y, x } = node.getBoundingClientRect() as DOMRect;
        const dy = (line - y) / scale;
        const dx = (line - x) / scale;
        const m = node.getMatrix() as SVGMatrix;

        node.setAttribute(
          "transform",
          `matrix(
                 ${m.a},
                 ${m.b},
                 ${m.c},
                 ${m.d},
                 ${m.e + (direction === "h" ? dx : 0)},
                 ${m.f + (direction === "v" ? dy : 0)})`
        );
      }
      const { width, height, bottom, right } =
        item.node.getBoundingClientRect() as DOMRect;
      if (direction === "v") {
        line = i === 0 ? bottom : line + height;
      }
      if (direction === "h") {
        line = i === 0 ? right : line + width;
      }
      line += distance * scale;
      i++;
    });
    this.select?.resetFrame(true);
  };

  filp = (direction: string) => {
    if (!direction) throw new Error("not set direction.");
    this.history?.add(this.history.actions().EDIT, "镜像翻转");
    this.select?.get().forEach((item: any) => {
      const node = item.node as SVGElement;
      const bbox = item.node.getBBox() as DOMRect;
      const m = node.getMatrix() as SVGMatrix;
      const a = m.a * (direction === "x" ? -1 : 1);
      const d = m.d * (direction === "y" ? -1 : 1);
      const e = m.e + (direction === "x" ? (bbox.x * 2 + bbox.width) * m.a : 0);
      const f =
        m.f + (direction === "y" ? (bbox.y * 2 + bbox.height) * m.d : 0);
      node.setAttribute(
        "transform",
        `matrix(
             ${a},
             ${m.b},
             ${m.c},
             ${d},
             ${e},
             ${f}
           )`
      );
    });
    this.select?.resetFrame(true);
  };

  nodeAlign = (direction: string, align: string) => {
    if (!direction) return;
    if (this.select && this.select.size() === 0) return;
    this.history?.add(this.history.actions().EDIT, "元素对齐");
    const tragetNode: any =
      this.select?.size() === 1 ? this.boardView : this.select?.frame();
    const pBox = tragetNode?.getBoundingClientRect() as DOMRect;

    const pX = pBox.x;
    const pY = pBox.y;
    const pR = pBox.right;
    const pB = pBox.bottom;
    const pHc = pBox.x + pBox.width / 2;
    const pVc = pBox.y + pBox.height / 2;

    this.select?.get().forEach((item: any) => {
      const m = new Matrix();
      const node = item.node;
      const box = node.getBoundingClientRect();
      const x = box.x;
      const y = box.y;
      const r = box.right;
      const b = box.bottom;
      const hc = box.x + box.width / 2;
      const vc = box.y + box.height / 2;
      const matrix = node.getMatrix() as SVGMatrix;
      const matrixIterator = node.parentElement.getMatrixIterator();
      m.add(matrix.a, matrix.b, matrix.c, matrix.d, matrix.e, matrix.f);
      m.translate(
        Number(
          (() => {
            if (direction === "v") return 0;
            switch (align) {
              case "left":
                return (pX - x) / matrix.a / matrixIterator.a;
              case "center":
                return (pHc - hc) / matrix.a / matrixIterator.a;
              case "right":
                return (pR - r) / matrix.a / matrixIterator.a;
            }
          })()
        ),
        Number(
          (() => {
            if (direction === "h") return 0;
            switch (align) {
              case "top":
                return (pY - y) / matrix.d / matrixIterator.d;
              case "center":
                return (pVc - vc) / matrix.d / matrixIterator.d;
              case "bottom":
                return (pB - b) / matrix.d / matrixIterator.d;
            }
          })()
        )
      );
      node.setAttribute(
        "transform",
        `matrix(${m.a},${m.b},${m.c},${m.d},${m.e},${m.f})`
      );
    });
    this.select?.resetFrame(true);
  };

  nodesPosition = (x: number, y: number) => {
    const frame = this.select?.frame() as SVGRectElement;
    const box = frame.getBox();
    const boardBox = this.boardView?.getBox();
    const dx = x * this.matrix.a - (box.x - boardBox.x);
    const dy = y * this.matrix.d - (box.y - boardBox.y);
    this.select?.get().forEach((item: any) => {
      const { a, d } = item.node.parentElement.getMatrixIterator();
      item.matrix = item.node.getMatrix();
      item.node.setAttribute(
        "transform",
        `matrix(
          ${item.matrix.a},
          ${item.matrix.b},
          ${item.matrix.c},
          ${item.matrix.d},
          ${item.matrix.e + dx / a},
          ${item.matrix.f + dy / d})`
      );
    });
  };

  switchLayer = (node?: Node) => {
    this.layer = node || this.svgRoot;
    this.#layerCallback?.forEach((func: Function) => {
      func(this.layer);
    });
    this.layerDisabled();
  };

  layerDisabled = () => {
    if (this.layer?.children.length === 0) return;
    const root = this.svgRoot as Node;
    const iterator = document.createNodeIterator(root, NodeFilter.SHOW_ELEMENT);
    let node = iterator.nextNode() as any;
    while (node !== null) {
      node.classList.remove("lux-disabled");
      if (
        node.localName !== "g" &&
        !node.contains(this.layer) &&
        !this.layer.contains(node) &&
        window.getComputedStyle(node).pointerEvents !== "none" &&
        this.currentType === this.tools().SELECT
      ) {
        node.classList.add("lux-disabled");
      }
      node = iterator.nextNode();
    }
  };
  focusNode = (node: Element) => {
    if (!node) return;
    if (this.select?.has(node)) return;
    if (node.localName === "g") {
      this.layer = node.parentElement;
      this.switchLayer(this.layer);
    }
    return new Promise((resolve) => {
      if (this.select && this.select?.size() > 0) this.select?.clear();
      this.select?.set(node, { node });
      this.select?.resetFrame(true);
      resolve(node);
    });
  };

  nodesMove = (direction: string, step: number, addHistory = true) => {
    if (addHistory)
      this.history?.add(this.history.actions().KEYBOARD_MOVE, "键盘移动");
    this.select?.get().forEach((item: any) => {
      const { node } = item;
      const m = node.getMatrix();
      const CTM = node.parentElement.getCTM() as SVGMatrix;
      let dataX = 0;
      let dataY = 0;
      switch (direction) {
        case "up":
          dataY = -step;
          break;
        case "down":
          dataY = step;
          break;
        case "left":
          dataX = -step;
          break;
        case "right":
          dataX = step;
          break;
      }

      const ad = CTM.a * CTM.d;
      const bc = CTM.b * CTM.c;
      const dx = (CTM.d / (ad - bc)) * dataX;
      const dy = (CTM.a / (ad - bc)) * dataY;
      const ne = dx - (dy / CTM.a) * CTM.c;
      const nf = dy - (dx / CTM.d) * CTM.b;
      const e = (m.e + ne).toFixed(9);
      const f = (m.f + nf).toFixed(9);

      node.setAttribute(
        "transform",
        `matrix(${m.a},${m.b},${m.c},${m.d},${e},${f})`
      );
    });
    this.select?.resetFrame(true);
  };

  front = () => {
    this.history?.add(this.history.actions().REORDER, "置于顶层");
    const tp_node = this.select?.first().node.parentNode;
    tp_node.prepend(this.select?.first().node);
    this.history?.end();
  };

  back = () => {
    this.history?.add(this.history.actions().REORDER, "置于底层");
    const bp_node = this.select?.first().node.parentNode;
    bp_node.append(this.select?.first().node);
    this.history?.end();
  };

  raise = () => {
    this.history?.add(this.history.actions().REORDER, "向上一层");
    const pre_node = this.select?.first().node.nextElementSibling;
    pre_node?.after(this.select?.first().node);
    this.history?.end();
  };

  lower = () => {
    this.history?.add(this.history.actions().REORDER, "向下一层");
    const next_node = this.select?.first().node.previousElementSibling;
    next_node?.before(this.select?.first().node);
    this.history?.end();
  };

  group = () => {
    this.history?.add(this.history.actions().GROUP, "编组");
    const fNode = this.select?.first().node;
    const gTag = new SVGGroup().node;
    fNode.before(gTag);
    this.select?.get().forEach((item: any) => {
      const c_node = item.node;
      gTag.append(c_node);
    });
    this.select?.reset();
    this.focusNode(gTag);
  };

  unGroup = (checkedsGroups?: Array<SVGGElement>) => {
    if (this.select?.checkedsGroups().size === 0) return;
    this.history?.add(this.history.actions().GROUP, "解除编组");
    const sNodes = [] as Array<Node>;
    const cGroups = checkedsGroups || this.select?.checkedsGroups();
    cGroups?.forEach((group: SVGElement) => {
      const pm = group.getMatrix();
      const iterator = document.createNodeIterator(
        group,
        NodeFilter.SHOW_ELEMENT
      );
      let node = iterator.nextNode() as any;
      while (node !== null) {
        if (node !== group) {
          const m = node.getMatrix() as SVGMatrix;
          const a = m.a * pm.a + m.c * pm.b;
          const b = m.b * pm.a + m.d * pm.b;
          const c = m.a * pm.c + m.c * pm.d;
          const d = m.b * pm.c + m.d * pm.d;
          const e = m.e * pm.sx + pm.e;
          const f = m.f * pm.sy + pm.f;
          node.setAttribute(
            "transform",
            `matrix(${a},${b},${c},${d},${e},${f})`
          );
          group.before(node);
          sNodes.push(node);
        }
        node = iterator.nextNode();
      }
      group.remove();
    });
    this.select?.reset();
    sNodes?.forEach((node: Node) => {
      this.select?.set(node, { node });
    });
    this.select?.resetFrame(true);
  };

  removeWatch = (action: Symbol, func: Function) => {
    if (this.actions().SELECTED === action) {
      this.#selectCallback.delete(func);
    } else if (this.actions().FRAME_RECT === action) {
      this.#rectCallback.delete(func);
    } else if (this.actions().UPDATED === action) {
      this.#updatedCallback.delete(func);
    } else if (this.actions().TOOL === action) {
      this.#toolCallback.delete(func);
    } else if (this.actions().SCALE === action) {
      this.#scaleCallback.delete(func);
    } else if (this.actions().LAYER === action) {
      this.#layerCallback.delete(func);
    } else if (this.actions().TEXT === action) {
      this.#textCallback.delete(func);
    } else if (this.actions().DRAW === action) {
      this.#drawCallback.delete(func);
    } else if (this.actions().VIEW === action) {
      this.#viewCallback.delete(func);
    } else if (this.actions().DRAG_BEFORE === action) {
      this.#dragBeforeCallback.delete(func);
    }
  };

  uninstall = () => {
    this.#listenScale?.disconnect();
    this.#listenSvgRoot?.disconnect();

    this.shape?.disabled(true);
    this.view?.disabled(true);
    this.freehand?.disabled(true);
    this.pan?.disabled(true);
    this.hover?.disabled(true);
    this.clipboard?.disabled(true);
    this.select?.disabled(true);
    this.resize?.disabled(true);

    this.view?.uninstall();
    this.select?.uninstall();
    this.history?.uninstall();
    this.hover?.uninstall();
    this.text?.uninstall();
    this.scale?.uninstall();
  };
}

export class SVGGroup {
  node: SVGGElement | SVGElement;
  constructor(attr?: any) {
    const node = createNode("g") as SVGGElement;
    if (attr) {
      Object.keys(attr).forEach((key) => {
        node.setAttribute(changeAttrName(key), attr[key]);
      });
    }
    this.node = node;
  }
  add(object: any) {
    this.node?.append(object.node);
  }
}

export class SVGRect {
  node: SVGRectElement | SVGElement;
  constructor(attr: any) {
    const node = createNode("rect") as SVGRectElement;
    Object.keys(attr).forEach((key) => {
      node.setAttribute(changeAttrName(key), attr[key]);
    });
    this.node = node;
  }
}

export class SVGCircle {
  node: SVGCircleElement | SVGElement;
  constructor(attr: any) {
    const node = createNode("circle") as SVGCircleElement;
    Object.keys(attr).forEach((key) => {
      node.setAttribute(changeAttrName(key), attr[key]);
    });
    this.node = node;
  }
}

export class SVGEllipse {
  node: SVGEllipseElement | SVGElement;
  constructor(attr: any) {
    const node = createNode("ellipse") as SVGEllipseElement;
    Object.keys(attr).forEach((key) => {
      node.setAttribute(changeAttrName(key), attr[key]);
    });
    this.node = node;
  }
}

export class SVGLine {
  node: SVGLineElement | SVGElement;
  constructor(attr?: any) {
    const node = createNode("line") as SVGLineElement;
    if (attr) {
      Object.keys(attr).forEach((key) => {
        node.setAttribute(changeAttrName(key), attr[key]);
      });
    }
    this.node = node;
  }
}

export class SVGText {
  node: SVGTextElement | SVGElement;
  constructor(attr?: any) {
    const node = createNode("text") as SVGTextElement;
    if (attr) {
      Object.keys(attr).forEach((key) => {
        if (key === "content") {
          node.innerHTML = attr[key];
          node.innerHTML = node.innerHTML.replace(/[\n]/gm, "");
        } else {
          node.setAttribute(changeAttrName(key), attr[key]);
        }
      });
    }
    this.node = node;
  }
}

export class SVGPath {
  node: SVGPathElement | SVGElement;
  constructor(attr?: any) {
    const node = createNode("path") as SVGPathElement;
    if (attr) {
      Object.keys(attr).forEach((key) => {
        if (key === "content") {
          node.innerHTML = attr[key];
        } else {
          node.setAttribute(changeAttrName(key), attr[key]);
        }
      });
    }
    this.node = node;
  }
}

export class SVGImage {
  node: SVGImageElement | SVGElement;
  constructor(attr: any) {
    const node = createNode("image") as SVGImageElement;
    node.setAttributeNS(
      "http://www.w3.org/1999/xlink",
      "xlink:href",
      attr.href
    );
    Object.keys(attr).forEach((key) => {
      if (key === "content") {
        node.innerHTML = attr[key];
      } else {
        node.setAttribute(changeAttrName(key), attr[key]);
      }
    });
    this.node = node;
  }
}
const createNode = (type: string): SVGElement => {
  const node = document.createElementNS("http://www.w3.org/2000/svg", type);
  node.setAttribute("data-fill", type === "text" ? "0:0:0:100" : "0:0:0:0");
  type !== "text" && node.setAttribute("data-stroke", "0:0:0:100");
  return node;
};

const changeAttrName = (str: string) => {
  return str
    .split("")
    .map((ite: string) => {
      return ite.toUpperCase() === ite ? (ite = "-" + ite.toLowerCase()) : ite;
    })
    .join("");
};

export const switchGroup = (node: SVGElement, bfMatrix: any) => {
  const afMatrix = (node.parentElement as any)?.getMatrixIterator();
  const angle = afMatrix.angle + bfMatrix.angle;
  const sx = bfMatrix.sx / afMatrix.sx;
  const sy = bfMatrix.sy / afMatrix.sy;

  const asX = afMatrix.a;
  const asY = afMatrix.d;
  const adX = afMatrix.e;
  const adY = afMatrix.f;

  // const curMatrix = node.getMatrixIterator();
  // const directionY = curMatrix.d * bfMatrix.d > 0 ? -1 : 1;
  // const directionX = curMatrix.a * bfMatrix.a > 0 ? -1 : 1;

  const dx = (bfMatrix.e - adX) / asX;
  const dy = (bfMatrix.f - adY) / asY;
  const m = node.getMatrix();
  const matrix = new Matrix();
  matrix.add(m.a, m.b, m.c, m.d, m.e, m.f);
  const m2 = matrix.rotate(angle);
  node.setAttribute(
    "transform",
    `matrix(
           ${m2.a * sx},
           ${m2.b * sx},
           ${m2.c * sy},
           ${m2.d * sy},
           ${m2.e * sx + dx},${m2.f * sy + dy})`
  );
};
