/**
 * @name path-data
 * @Description: get svg path values
 * @Autor: cbb
 * @Date: 2022-11-30 16:33:41
 * @LastEditTime: 2022-12-03 18:33:41
 */
declare global {
  interface SVGPathElement {
    getPathData: Function;
    toUpperCase: Function;
  }
  interface SVGRectElement {
    getPathData: Function;
  }
  interface SVGCircleElement {
    getPathData: Function;
  }
  interface SVGEllipseElement {
    getPathData: Function;
  }
  interface SVGLineElement {
    getPathData: Function;
  }
  interface SVGPolygonElement {
    getPathData: Function;
  }
  interface SVGPolylineElement {
    getPathData: Function;
  }
}

const d_Type = {
  Z: 'Z',
  M: 'M',
  L: 'L',
  C: 'C',
  Q: 'Q',
  A: 'A',
  H: 'H',
  V: 'V',
  S: 'S',
  T: 'T',
  z: 'Z',
  m: 'm',
  l: 'l',
  c: 'c',
  q: 'q',
  a: 'a',
  h: 'h',
  v: 'v',
  s: 's',
  t: 't',
};

const pathArrConvert = (arr: Array<any>) => {
  const e = [] as Array<any>;

  let i = 0;
  while (i <= arr.length) {
    const u = arr[i];
    if (d_Type.hasOwnProperty(u)) {
      if (u.toLowerCase() === 'v' || u.toLowerCase() === 'h') {
        e.push({
          type: u,
          values: [Number(arr[i + 1])],
        });
      }
      if (u.toLowerCase() === 'm' || u.toLowerCase() === 'l' || u.toLowerCase() === 't') {
        e.push({
          type: u,
          values: [Number(arr[i + 1]), Number(arr[i + 2])],
        });
      }
      if (u.toLowerCase() === 'q' || u.toLowerCase() === 's') {
        e.push({
          type: u,
          values: [Number(arr[i + 1]), Number(arr[i + 2]), Number(arr[i + 3]), Number(arr[i + 4])],
        });
      }
      if (u.toLowerCase() === 'c') {
        e.push({
          type: u,
          values: [
            Number(arr[i + 1]),
            Number(arr[i + 2]),
            Number(arr[i + 3]),
            Number(arr[i + 4]),
            Number(arr[i + 5]),
            Number(arr[i + 6]),
          ],
        });
      }
      if (u.toLowerCase() === 'a') {
        e.push({
          type: u,
          values: [
            Number(arr[i + 1]),
            Number(arr[i + 2]),
            Number(arr[i + 3]),
            Number(arr[i + 4]),
            Number(arr[i + 5]),
            Number(arr[i + 6]),
            Number(arr[i + 7]),
          ],
        });
      }
      if (u.toLowerCase() === 'z') {
        e.push({
          type: u,
          values: [],
        });
      }
    }
    i++;
  }
  return e;
};

if (!SVGPathElement.prototype.getPathData) {
  SVGPathElement.prototype.getPathData = function () {
    const d = this.getAttribute('d') as string;
    const d_value = d
      .replace(/[\n\r]/g, '')
      .replace(/-/g, ' -')
      .replace(/(\d*\.)(\d+)(?=\.)/g, '$1$2 ')
      .replace(/([A-Za-z])(\d+\.\d+ || \d+)/g, '$1 $2 ')
      .replace(/(\d+\.\d+ || \d+)([A-Za-z])/g, '$1 $2 ')
      .trim()
      .split(/\s*,|\s+/);
    const e = pathArrConvert(d_value);
    return e;
  };
}

if (!SVGRectElement.prototype.getPathData) {
  SVGRectElement.prototype.getPathData = function () {
    const t = this.x.baseVal.value;
    const s = this.y.baseVal.value;
    const r = this.width.baseVal.value;
    const a = this.height.baseVal.value;
    let n = this.hasAttribute('rx') ? this.rx.baseVal.value : this.ry.baseVal.value;
    let u = this.hasAttribute('ry') ? this.ry.baseVal.value : this.rx.baseVal.value;
    n > r / 2 && (n = r / 2);
    u > a / 2 && (u = a / 2);
    const i = [
      {
        type: 'M',
        values: [t + n, s],
      },
      {
        type: 'H',
        values: [t + r - n],
      },
      {
        type: 'A',
        values: [n, u, 0, 0, 1, t + r, s + u],
      },
      {
        type: 'V',
        values: [s + a - u],
      },
      {
        type: 'A',
        values: [n, u, 0, 0, 1, t + r - n, s + a],
      },
      {
        type: 'H',
        values: [t + n],
      },
      {
        type: 'A',
        values: [n, u, 0, 0, 1, t, s + a - u],
      },
      {
        type: 'V',
        values: [s + u],
      },
      {
        type: 'A',
        values: [n, u, 0, 0, 1, t + n, s],
      },
      {
        type: 'Z',
        values: [],
      },
    ];
    return i;
  };
}

if (!SVGCircleElement.prototype.getPathData) {
  SVGCircleElement.prototype.getPathData = function () {
    const t = this.cx.baseVal.value,
      s = this.cy.baseVal.value,
      r = this.r.baseVal.value,
      a = [
        {
          type: 'M',
          values: [t + r, s],
        },
        {
          type: 'A',
          values: [r, r, 0, 0, 1, t, s + r],
        },
        {
          type: 'A',
          values: [r, r, 0, 0, 1, t - r, s],
        },
        {
          type: 'A',
          values: [r, r, 0, 0, 1, t, s - r],
        },
        {
          type: 'A',
          values: [r, r, 0, 0, 1, t + r, s],
        },
        {
          type: 'Z',
          values: [],
        },
      ];
    return a;
  };
}

if (!SVGEllipseElement.prototype.getPathData) {
  SVGEllipseElement.prototype.getPathData = function () {
    const t = this.cx.baseVal.value,
      s = this.cy.baseVal.value,
      r = this.rx.baseVal.value,
      a = this.ry.baseVal.value,
      n = [
        {
          type: 'M',
          values: [t + r, s],
        },
        {
          type: 'A',
          values: [r, a, 0, 0, 1, t, s + a],
        },
        {
          type: 'A',
          values: [r, a, 0, 0, 1, t - r, s],
        },
        {
          type: 'A',
          values: [r, a, 0, 0, 1, t, s - a],
        },
        {
          type: 'A',
          values: [r, a, 0, 0, 1, t + r, s],
        },
        {
          type: 'Z',
          values: [],
        },
      ];
    return n;
  };
}

if (!SVGLineElement.prototype.getPathData) {
  SVGLineElement.prototype.getPathData = function () {
    return [
      {
        type: 'M',
        values: [this.x1.baseVal.value, this.y1.baseVal.value],
      },
      {
        type: 'L',
        values: [this.x2.baseVal.value, this.y2.baseVal.value],
      },
    ];
  };
}

if (!SVGPolylineElement.prototype.getPathData) {
  SVGPolylineElement.prototype.getPathData = function () {
    const e = [] as Array<any>;
    for (let t = 0; t < this.points.numberOfItems; t += 1) {
      const s = this.points.getItem(t);
      e.push({
        type: 0 === t ? 'M' : 'L',
        values: [s.x, s.y],
      });
    }
    return e;
  };
}

if (!SVGPolygonElement.prototype.getPathData) {
  SVGPolygonElement.prototype.getPathData = function () {
    const e = [] as Array<any>;
    for (let t = 0; t < this.points.numberOfItems; t += 1) {
      const s = this.points.getItem(t);
      e.push({
        type: 0 === t ? 'M' : 'L',
        values: [s.x, s.y],
      });
    }
    return (
      e.push({
        type: 'Z',
        values: [],
      }),
      e
    );
  };
}

if (!SVGPathElement.prototype.toUpperCase) {
  SVGPathElement.prototype.toUpperCase = function () {
    const bd = this.getAttribute('d') as string;

    if (
      !bd?.includes(d_Type.A) &&
      !bd?.includes(d_Type.a) &&
      !bd?.includes(d_Type.t) &&
      !bd?.includes(d_Type.T) &&
      !bd?.includes(d_Type.q) &&
      !bd?.includes(d_Type.Q) &&
      !bd?.includes(d_Type.h) &&
      !bd?.includes(d_Type.H) &&
      !bd?.includes(d_Type.v) &&
      !bd?.includes(d_Type.V) &&
      !bd?.includes(d_Type.s) &&
      !bd?.includes(d_Type.S) &&
      !bd?.includes(d_Type.m) &&
      !bd?.includes(d_Type.l) &&
      !bd?.includes(d_Type.c)
    )
      return;

    let d = '';
    let dx = 0;
    let dy = 0;

    const pData = this.getPathData();
    pData.forEach((item: any) => {
      const type = item.type.toUpperCase();
      switch (item.type) {
        case 'm':
          d += `${type} ${dx + item.values[0]} ${dy + item.values[1]} `;
          dx += item.values[0];
          dy += item.values[1];
          break;
        case 'M':
        case 'L':
        case 'T':
          d += `${type === 'T' ? 'L' : type} ${item.values[0]} ${item.values[1]} `;
          dx = item.values[0];
          dy = item.values[1];
          break;
        case 'h':
          d += `L ${dx + item.values[0]} ${dy} `;
          dx += item.values[0];
          break;
        case 'H':
          d += `L ${item.values[0]} ${dy} `;
          dx = item.values[0];
          break;
        case 'v':
          d += `L ${dx} ${dy + item.values[0]} `;
          dy += item.values[0];
          break;
        case 'V':
          d += `L ${dx} ${item.values[0]} `;
          dy = item.values[0];
          break;
        case 'l':
        case 't':
          d += `L ${dx + item.values[0]} ${dy + item.values[1]} `;
          dx += item.values[0];
          dy += item.values[1];
          break;
        case 'q':
          {
            const x1 = (dx + item.values[0] - dx) * (2 / 3) + dx;
            const y1 = (dy + item.values[1] - dy) * (2 / 3) + dy;
            const x2 = (dx + item.values[2] - dx) * (1 / 3) + x1;
            const y2 = (dy + item.values[3] - dy) * (1 / 3) + y1;
            d += `C ${x1} ${y1} ${x2} ${y2} ${dx + item.values[2]} ${dy + item.values[3]}`;
            dx += item.values[2];
            dy += item.values[3];
          }
          break;
        case 'Q':
          {
            const x1 = (item.values[0] - dx) * (2 / 3) + dx;
            const y1 = (item.values[1] - dy) * (2 / 3) + dy;
            const x2 = (item.values[2] - dx) * (1 / 3) + x1;
            const y2 = (item.values[3] - dy) * (1 / 3) + y1;
            d += `C ${x1} ${y1} ${x2} ${y2} ${item.values[2]} ${item.values[3]}`;
            dx = item.values[2];
            dy = item.values[3];
          }
          break;
        case 's':
          d += `C ${dx} ${dy} ${dx + item.values[0]} ${dy + item.values[1]} ${
            dx + item.values[2]
          } ${dy + item.values[3]}`;
          dx += item.values[2];
          dy += item.values[3];
          break;
        case 'S':
          d += `C ${dx} ${dy} ${item.values[0]} ${item.values[1]} ${item.values[2]} ${item.values[3]} `;
          dx = item.values[2];
          dy = item.values[3];
          break;
        case 'c':
          d += `${type} ${dx + item.values[0]} ${dy + item.values[1]} ${dx + item.values[2]} ${
            dy + item.values[3]
          } ${dx + item.values[4]} ${dy + item.values[5]} `;
          dx += item.values[4];
          dy += item.values[5];
          break;
        case 'C':
          d += `${type} ${item.values[0]} ${item.values[1]} ${item.values[2]} ${item.values[3]} ${item.values[4]} ${item.values[5]} `;
          dx = item.values[4];
          dy = item.values[5];
          break;
        case 'a':
          d += `${type} ${item.values[0]} ${item.values[1]} ${item.values[2]} ${item.values[3]} ${
            item.values[4]
          } ${dx + item.values[5]} ${dy + item.values[6]}`;
          dx += item.values[5];
          dy += item.values[6];
          break;
        case 'A':
          d += `${type} ${item.values[0]} ${item.values[1]} ${item.values[2]} ${item.values[3]} ${item.values[4]} ${item.values[5]} ${item.values[6]} `;
          dx = item.values[5];
          dy = item.values[6];
          break;
        case 'z':
        case 'Z':
          d += type;
          break;
      }
    });

    this.setAttribute('d', d);
  };
}

export {};
