import "./App.css";
import "./global";
import Toolbar from "./views/toolbar";
import Menus from "./views/menus";
import Actionbar from "./views/actionbar";
import Contextmenu from "./views/contextmenu";
import THREE_PREVIEW from "./views/3D-preview";
import { generate3DData } from "./views/3D-preview/data";
import React, { useRef} from "react";
import { Stage } from "./lux";

interface State {
  stage: Stage | undefined;
  type: number;
  threeData: any;
  has3D: boolean;
}

interface Props {}

class App extends React.Component<Props, State> {


  public state: State;
  constructor(props: Props) {
    super(props);
    this.state = {
      stage: void 0,
      type: 2,
      threeData: void 0,
      has3D: false,
    };
  }


  componentDidMount() {
   

      const stage = ((window as any).stage = new Stage());
      const board = document.querySelector("#board") as HTMLElement;
      const box = board?.getBoundingClientRect();
      stage.init(board, 1980, 1200);
      
      let height = 600;
      let width = 600;
      try {
        width = Number(box?.height || 680) - 80;
        height = Number(box?.height || 680) - 80;
      } catch (error) {}
      stage.setViewSize(width, height);
      stage.setBoardSize(1, 20, 20);

      stage.watch(stage.actions().UPDATED, () => {
        const border = stage?.svgRoot?.querySelector("#边框");
        this.setState({
          has3D: border ? true : false,
        });
      });
    
      this.setState({
        stage,
      });
  }

  changeType = (type: number) => {

    this.setState({
      type,
    });

    if (type === 3) {
      const b = this.state.stage?.svgRoot?.querySelector("#边框");
      const A = b?.querySelector("#A");
      const B = b?.querySelector("#B");
      const l =
        Number(A?.getAttribute("width")) / Number(this.state.stage?.mmUnit);
      const w =
        Number(B?.getAttribute("width")) / Number(this.state.stage?.mmUnit);
      const h =
        Number(A?.getAttribute("height")) / Number(this.state.stage?.mmUnit);
      (window as any).threeData = generate3DData(l, w, h, 30);
    }
  };

  componentWillUnmount() {}

  render = () => {
    return (
      <div className="App">
        {this.state.stage && <Menus />}
        <div id="editor" className="flex">
          {this.state.stage && <Toolbar />}
          <div id="board" className="svg"></div>
          {this.state.stage && <Contextmenu />}
          {this.state.stage && <Actionbar />}
        </div>
        {this.state.type === 3 && <THREE_PREVIEW />}
        <div className="toggle-3d-wrapper">
          <div className="toggle-3d-inner flex flex-align-center">
            <div
              onClick={() => this.changeType(2)}
              className={
                this.state.type === 2
                  ? "toggle-3d-item toggle-3d-item-active"
                  : "toggle-3d-item"
              }
            >
              <span className="flex flex-justify-center flex-align-center">
                2D
              </span>
            </div>
            <div
              onClick={() => this.changeType(3)}
              className={
                this.state.type === 3
                  ? "toggle-3d-item toggle-3d-item-active"
                  : this.state.has3D ? "toggle-3d-item" : "toggle-3d-item disabled"
              }
            >
              <span className="flex flex-justify-center flex-align-center">
                3D
              </span>
            </div>
          </div>
        </div>
      </div>
    );
  };
}

export default App;
