import { Stage, SVGImage, SVGText } from "..";
class Clipboard {
  #stage: Stage;
  #isDisabled = false;
  constructor(stage: Stage) {
    if (!stage) throw new Error("stage not found!");
    this.#stage = stage;
  }
  cut = () => {
    if (this.#stage.select?.size() === 0 || this.#isDisabled) return;
    this.copy();
    this.#stage.select?.get().forEach((item: any) => item.node.remove());
    this.#stage.select?.clear();
    this.#stage.select?.reset();
  };
  copy = () => {
    if (this.#stage.select?.size() === 0 || this.#isDisabled) return;
    let outerHTML = "";
    let defsHTML = "";
    this.#stage.select?.get().forEach((item: any) => {
      outerHTML += item.node.outerHTML;
    });

    const defs = this.#stage.svgRoot?.querySelector("defs");
    defs?.childNodes.forEach((node: any) => {
      if (outerHTML.includes(node.id)) {
        defsHTML += node.outerHTML;
      }
    });
    const type = "text/plain";
    const svgHTML = defs
      ? `<svg>${`<defs>${defsHTML}</defs>`}${outerHTML}</svg>`
      : `<svg>${outerHTML}</svg>`;

    const blob = new Blob([svgHTML], { type });
    const data = [new ClipboardItem({ [type]: blob })];
    window.navigator.clipboard.write(data);
  };
  paste = async () => {
    if (this.#isDisabled) return;
    const pasteCenter = () => {
      this.#stage.history?.add(this.#stage.history.actions().ADD, "粘贴");
      const { a } = this.#stage.matrix;
      const screenBox = this.#stage.stage?.getBoundingClientRect() as DOMRect;
      const box = this.#stage.select
        ?.frame()
        ?.getBoundingClientRect() as DOMRect;
      const sCX = screenBox.x + screenBox.width / 2;
      const sCY = screenBox.y + screenBox.height / 2;
      const rCX = box.x + box.width / 2;
      const rCY = box.y + box.height / 2;
      const e = (sCX - rCX) / a;
      const f = (sCY - rCY) / a;
      this.#stage.select?.get().forEach((item: any) => {
        const { node } = item;
        const matrix = node.getMatrix();

        node.setAttribute(
          "transform",
          `matrix(${matrix.a} ${matrix.b} ${matrix.c} ${matrix.d} ${
            matrix.e + e
          } ${matrix.f + f})`
        );
      });

      this.#stage.select?.resetFrame(true);
    };

    const pasteText = (content: string) => {
      const rc = content
        .replace(/\t/g, "")
        .replace(
          /[\n]/gm,
          `<tspan x="${0}" dy="${"1em"}">&ZeroWidthSpace;</tspan>`
        );

      const text = new SVGText({
        x: 0,
        y: 0,
        content: rc.replace("\n", ""),
        fontSize: this.#stage.options.fontSize,
      });

      if (this.#stage.currentType === this.#stage.tools().SELECT) {
        this.#stage.layer.append(text.node);
      } else {
        this.#stage.svgRoot?.append(text.node);
      }
      this.#stage.select?.reset();
      this.#stage.select?.set(text.node, { node: text.node });
      this.#stage.select?.resetFrame(true);
      pasteCenter();
    };

    const pasteHTML = (svg: HTMLElement) => {
      this.#stage.select?.reset();
      svg?.childNodes.forEach((node: any) => {
        node = node.cloneNode(true);
        if (this.#stage.currentType === this.#stage.tools().SELECT) {
          this.#stage.layer.append(node);
        } else {
          this.#stage.svgRoot?.append(node);
        }
        if (this.#stage.CAN_CEHCKED_NODE.includes(node.localName))
          this.#stage.select?.set(node, { node });
      });
      this.#stage.select?.resetFrame(true);
      pasteCenter();
    };

    const pasteImage = (href: string) => {
      const img = new Image();
      img.src = href;
      img.onload = () => {
        const width = img.width;
        const height = img.height;
        const image = new SVGImage({
          href,
          x: 0,
          y: 0,
          width,
          height,
        });
        if (this.#stage.currentType === this.#stage.tools().SELECT) {
          this.#stage.layer.append(image.node);
        } else {
          this.#stage.svgRoot?.append(image.node);
        }
        this.#stage.select?.reset();
        this.#stage.select?.set(image.node, { node: image.node });
        this.#stage.select?.resetFrame(true);
        pasteCenter();
      };
    };

    const { type, text, svg, href }: any = await this.pasteTypes();

    switch (type) {
      case "text/plain":
        pasteText(text);
        break;
      case "text/html":
        pasteHTML(svg);
        break;
      case "image/png":
        pasteImage(href);
        break;
    }
  };

  pasteTypes = () => {
    return new Promise(async (resolve) => {
      const clipboard = await window.navigator.clipboard.read();
      const item = clipboard[0];
      if (item.types.includes("image/png")) {
        // const textBlob = await item.getType("text/plain");
        // const text = await textBlob.text();
        // if (text.includes("</svg>")) return resolve(this.#text2HTML(text));
        const blob = await item.getType("image/png");
        const href = URL.createObjectURL(blob);
        resolve({
          type: "image/png",
          href,
        });
      } else if (item.types.includes("text/html")) {
        const htmlBlob = await item.getType("text/plain");
        const textHtml = await htmlBlob.text();
        if (textHtml.includes("</svg>"))
          return resolve(this.#text2HTML(textHtml));

        const textBlob = await item.getType("text/plain");
        const text = await textBlob.text();
        resolve({
          type: "text/plain",
          text,
        });
      } else if (item.types.includes("text/plain")) {
        const textBlob = await item.getType("text/plain");
        const text = await textBlob.text();
        if (text.includes("</svg>")) return resolve(this.#text2HTML(text));
        resolve({
          type: "text/plain",
          text,
        });
      }
    });
  };

  #text2HTML = (text: string) => {
    const domparser = new DOMParser();
    const doc = domparser.parseFromString(text, "text/html");
    return {
      type: "text/html",
      svg: doc.body.querySelector("svg"),
    };
  };

  disabled = (isDisabled: boolean) => {
    this.#isDisabled = isDisabled;
  };
}
export default Clipboard;
