import { Stage } from "..";

class Freehand {
  #stage: Stage;
  #freehandLine?: SVGPolylineElement;
  #isDrawing = false;
  #isMoveing = false;
  #point?: DOMPoint;
  #splineHud?: SVGElement;
  #grippiesGroup?: SVGElement;
  #pathPoint = new Map();
  #freeHand = new Map();
  #sTarget?: SVGElement;
  #eTarget?: SVGElement;
  #startX = 0;
  #startY = 0;
  #shiftKey = false;
  #isDisabled = true;
  constructor(stage: Stage) {
    if (!stage) throw new Error("stage not found!");
    this.#stage = stage;
    this.#install();
  }
  #install() {
    this.#stage.stage?.addEventListener("mousedown", this.#mousedown);
    document.addEventListener("mousemove", this.#mousemove);
    document.addEventListener("mouseup", this.#mouseup);

    this.#splineHud = document.querySelector("#spline-hud") as SVGElement;
    this.#grippiesGroup = this.#splineHud?.querySelector(
      "[uid='primary-node-grippies']"
    ) as SVGElement;

    this.#freehandLine = document.querySelector(
      "#freehand-hud polyline"
    ) as SVGPolylineElement;
    this.#watch();
  }
  #watch = () => {
    this.#stage.watch(
      this.#stage.actions().UPDATED,
      (record: MutationRecord[]) => {
        if (this.#isDisabled) return;
        record.forEach((record: MutationRecord) => {
          this.#addNodes(record);
          this.#removeNodes(record);
        });
      }
    );

    this.#stage.history?.watch((operation: Symbol) => {
      if (
        this.#stage.currentType !== this.#stage.tools().FREEHAND ||
        operation === this.#stage.history?.actions().ADD
      )
        return;
      this.reset();
      this.clearPoints();
      this.clearGrippies();
      this.#initGrippies();
    });
  };
  #addNodes = (record: MutationRecord) => {
    record.addedNodes.forEach((node: Node) => {
      const element = node as Element;
      this.#initGrippies(element);
    });
  };
  #removeNodes = (record: MutationRecord) => {
    record.removedNodes.forEach((node: Node) => {
      this.#freeHand?.get(node)?.forEach((grippie: Element) => {
        grippie.remove();
      });
    });
  };
  #initGrippies(element?: Element) {
    const fragment = document.createDocumentFragment();
    this.#stage.select?.checkedsPaths().forEach((node: any) => {
      if (element && element !== node) return;
      const d = node.getAttribute("d") as string;
      if (d.toUpperCase().includes("Z")) return;
      const path = node as SVGPathElement;

      const pData = path.getPathData();
      const sCx = pData[0].values[0];
      const sCy = pData[0].values[1];
      const lastItem = pData[pData.length - 1];

      const { eCx, eCy } = (() => {
        let eCx = 0;
        let eCy = 0;
        switch (lastItem.type) {
          case "L":
            eCx = lastItem.values[0];
            eCy = lastItem.values[1];
            break;
          case "C":
            eCx = lastItem.values[4];
            eCy = lastItem.values[5];
            break;
        }
        return { eCx, eCy };
      })();

      const CTM = node.getCTM() as SVGMatrix;
      const cCTM = (this.#stage.canvas as SVGAElement)?.getCTM() as SVGMatrix;

      const sx = CTM.a / cCTM.a;
      const sy = CTM.d / cCTM.d;

      const dSCx = (1 - sx) * sCx;
      const dSCy = (1 - sy) * sCy;
      const dECx = (1 - sx) * eCx;
      const dECy = (1 - sy) * eCy;

      const dx = (CTM.e - cCTM.e) / cCTM.a;
      const dy = (CTM.f - cCTM.f) / cCTM.d;

      const nSCx = sCx + dx - dSCx;
      const nSCy = sCy + dy - dSCy;

      const nECx = eCx + dx - dECx;
      const nECy = eCy + dy - dECy;

      const sp = this.#createGrippies(nSCx, nSCy, "start", path);
      const ep = this.#createGrippies(nECx, nECy, "end", path);
      fragment.append(sp);
      fragment.append(ep);

      this.#freeHand.set(path, [sp, ep]);
    });

    this.#grippiesGroup?.append(fragment);
  }

  #createGrippies(x: number, y: number, pos: string, node: SVGElement) {
    const g = this.#createSvgNode("g");
    g.classList.add("primary-node-grippie");
    g.setAttribute("data-position", pos);

    const oC = this.#createSvgNode("circle") as SVGCircleElement;
    oC.classList.add("outer-circle");
    const iC = this.#createSvgNode("circle") as SVGCircleElement;
    iC.classList.add("inner-circle");

    oC.setAttribute("cx", x.toFixed(9));
    oC.setAttribute("cy", y.toFixed(9));
    iC.setAttribute("cx", x.toFixed(9));
    iC.setAttribute("cy", y.toFixed(9));

    const { a: scale } = this.#stage.matrix;
    const oSize = (this.#stage.options.outerCircleSize || 5) / scale;
    const iSize = (this.#stage.options.innerCircleSize || 1.5) / scale;
    oC.setAttribute("r", oSize.toFixed(9));
    iC.setAttribute("r", iSize.toFixed(9));

    g.append(oC);
    g.append(iC);
    this.#pathPoint.set(g, node);

    return g;
  }
  #mousedown = (ev: MouseEvent) => {
    if (ev.button !== 0 || this.#isDisabled) return;
    this.#isDrawing = true;
    const { clientX, clientY, target } = ev;
    const t = target as SVGElement;
    if (this.#grippiesGroup?.contains(t)) this.#sTarget = t;

    this.#startX = clientX;
    this.#startY = clientY;
  };
  #mousemove = (ev: MouseEvent) => {
    if (!this.#isDrawing) return;
    const { clientX, clientY, shiftKey } = ev;
    if (shiftKey) this.#shiftKey = shiftKey;

    const d =
      Math.abs(this.#startX - clientX) + Math.abs(this.#startY - clientY);

    if (d > 4) {
      this.#isMoveing = true;
    }

    const fh = this.#freehandLine as any;
    this.#point = fh.ownerSVGElement.createSVGPoint() as DOMPoint;
    const { x, y } = this.#stage.stage?.getBoundingClientRect() as DOMRect;
    const { sx, sy, e, f } = this.#stage.canvas?.getMatrix();
    this.#point.x = (clientX - x - e) / sx;
    this.#point.y = (clientY - y - f) / sy;
    if (this.#shiftKey) {
      const startPoint = fh.ownerSVGElement.createSVGPoint() as DOMPoint;
      startPoint.x = (this.#startX - x - e) / sx;
      startPoint.y = (this.#startY - y - f) / sy;

      if (this.#freehandLine && this.#freehandLine?.points.length > 1) {
        this.clearPoints();
        this.#freehandLine?.points.appendItem(startPoint);
        this.#freehandLine?.points.appendItem(this.#point);
      } else {
        this.#freehandLine?.points.appendItem(startPoint);
        this.#freehandLine?.points.appendItem(this.#point);
      }
    } else {
      this.#freehandLine?.points.appendItem(this.#point);
    }
  };
  #mouseup = (ev: MouseEvent) => {
    if (!this.#isDrawing) return;
    this.#isDrawing = false;
    if (!this.#isMoveing) {
      this.#shiftKey = false;
      this.clearPoints();
      this.#sTarget = void 0;
      this.#eTarget = void 0;
      this.#isMoveing = false;
      return;
    }
    this.#isMoveing = false;

    const pData = this.#freehandLine?.getPathData();

    const t = ev.target as SVGElement;
    const { x, y } = this.#stage.stage?.getBoundingClientRect() as DOMRect;
    const { sx, sy, e, f } = this.#stage.canvas?.getMatrix();
    const cx = (ev.clientX - x - e) / sx;
    const cy = (ev.clientY - y - f) / sy;

    if (this.#grippiesGroup?.contains(t)) this.#eTarget = t;

    if (this.#sTarget || this.#eTarget) {
      this.#stage.history?.add(this.#stage.history.actions().EDIT, "手绘");
      const sPath = this.#pathPoint.get(this.#sTarget?.parentNode);
      const ePath = this.#pathPoint.get(this.#eTarget?.parentNode);

      const sp = this.#sTarget?.parentElement?.dataset.position;
      const ep = this.#eTarget?.parentElement?.dataset.position;

      // const sd = sPath?.getAttribute('d') as string;
      // const ed = ePath?.getAttribute('d') as string;

      //same node
      if (sPath === ePath) {
        //close
        if (this.#shiftKey && ep === "start") pData.pop();
        let d = sPath.getAttribute("d") as string;
        let D = "";

        const CTM = sPath.getCTM() as SVGMatrix;
        const cCTM = (this.#stage.canvas as SVGAElement)?.getCTM() as SVGMatrix;
        const sx = CTM.a / cCTM.a;
        const sy = CTM.d / cCTM.d;

        if (sp === "start") {
          d = d.replace("M", "L");
          pData.reverse();
          const x = pData[0].values[0];
          const y = pData[0].values[1];
          D += `M ${(x - (CTM.e - cCTM.e) / cCTM.a) / sx} ${
            (y - (CTM.f - cCTM.f) / cCTM.d) / sy
          } `;
          if (this.#shiftKey) {
            D += this.#convert2Path(pData, cCTM, cCTM);
          } else {
            D += this.#l2c(pData, CTM, cCTM);
          }
        } else {
          pData.shift();
          if (this.#shiftKey) {
            d += this.#convert2Path(pData, CTM, cCTM);
          } else {
            d += this.#l2c(pData, CTM, cCTM);
          }
        }
        sPath.setAttribute("d", D + " " + d + "Z");
        this.#sTarget?.parentElement?.remove();
        this.#eTarget?.parentElement?.remove();
      } else {
        if (sPath && ePath) {
          //merge

          let d = "";
          const sp = this.#sTarget?.parentElement?.dataset.position;
          const ep = this.#eTarget?.parentElement?.dataset.position;
          const sPdata = sPath.getPathData();
          const ePdata = ePath.getPathData();

          const CTM = sPath.getCTM() as SVGMatrix;
          const cCTM = (
            this.#stage.canvas as SVGAElement
          )?.getCTM() as SVGMatrix;

          if (sp === "start" && ep === "end") {
            sPdata.reverse().forEach((item: any, index: number) => {
              const { type, values } = item;
              if (index === 0) d += `M ${values[0]} ${values[1]}`;
              d += `L ${values[0]} ${values[1]}`;
            });
            ePdata.reverse().forEach((item: any, index: number) => {
              const { type, values } = item;
              d += `L ${values[0]} ${values[1]}`;
            });
          } else if (sp === "start" && ep === "start") {
            sPdata.reverse().forEach((item: any, index: number) => {
              const { type, values } = item;
              if (index === 0) d += `M ${values[0]} ${values[1]}`;
              d += `L ${values[0]} ${values[1]}`;
            });
            d += ePath.getAttribute("d").replace("M", "L");
          } else if (sp === "end" && ep === "end") {
            d = sPath.getAttribute("d");
            ePdata.reverse().forEach((item: any) => {
              const { type, values } = item;
              d += `L ${values[0]} ${values[1]}`;
            });
          } else if (sp === "end" && ep === "start") {
            d = sPath.getAttribute("d");
            const ePdata = ePath.getPathData();
            ePdata.forEach((item: any) => {
              const { type, values } = item;
              d += `L ${values[0]} ${values[1]}`;
            });
          }

          this.#stage.select?.delete(ePath);
          ePath.remove();
          sPath.setAttribute("d", d);
        } else if (!sPath || !ePath) {
          //expand
          if (sp === "start" || ep === "start") {
            const positive = sp === "start" ? true : false;
            const path = sPath || ePath;

            const CTM = path.getCTM() as SVGMatrix;
            const cCTM = (
              this.#stage.canvas as SVGAElement
            )?.getCTM() as SVGMatrix;
            const sx = CTM.a / cCTM.a;
            const sy = CTM.d / cCTM.d;

            let d = path.getAttribute("d");
            d = d.replace("M", "L");
            let D = "";

            if (positive) pData.reverse();

            const x = pData[0].values[0];
            const y = pData[0].values[1];
            D += `M ${(x - (CTM.e - cCTM.e) / cCTM.a) / sx} ${
              (y - (CTM.f - cCTM.f) / cCTM.d) / sy
            } `;
            if (this.#shiftKey) {
              D += this.#convert2Path(pData, CTM, cCTM);
            } else {
              D += this.#l2c(pData, CTM, cCTM);
            }

            path.setAttribute("d", D + " " + d);

            const g = this.#sTarget?.parentNode || this.#eTarget?.parentNode;

            const sCx = pData[0].values[0];
            const sCy = pData[0].values[1];

            g?.childNodes.forEach((ChildNode: ChildNode) => {
              if (ChildNode.nodeType === 3) return;
              const element = ChildNode as Element;
              element?.setAttribute(
                "cx",
                positive ? cx.toFixed(9) : sCx.toFixed(9)
              );
              element?.setAttribute(
                "cy",
                positive ? cy.toFixed(9) : sCy.toFixed(9)
              );
            });
          }
          if (sp === "end" || ep === "end") {
            const positive = sp === "end" ? true : false;

            const path = sPath || ePath;
            let d = path.getAttribute("d");

            const CTM = path.getCTM() as SVGMatrix;
            const cCTM = (
              this.#stage.canvas as SVGAElement
            )?.getCTM() as SVGMatrix;

            if (!positive) pData.reverse();

            if (this.#shiftKey) {
              d += this.#convert2Path(pData, CTM, cCTM);
            } else {
              d += this.#l2c(pData, CTM, cCTM);
            }

            path.setAttribute("d", d);
            const sCx = pData[pData.length - 1].values[0];
            const sCy = pData[pData.length - 1].values[1];

            const g = this.#sTarget?.parentNode || this.#eTarget?.parentNode;
            g?.childNodes.forEach((ChildNode: any) => {
              if (ChildNode.nodeType === 3) return;
              ChildNode?.setAttribute(
                "cx",
                positive ? cx.toFixed(9) : sCx.toFixed(9)
              );
              ChildNode?.setAttribute(
                "cy",
                positive ? cy.toFixed(9) : sCy.toFixed(9)
              );
            });
          }
        }
      }
      this.#stage.history?.end();
    } else {
      let d = "";
      if (this.#shiftKey) {
        for (let i = 0; i < pData.length; i++) {
          d += `${i === 0 ? "M" : "L"} ${pData[i].values[0]} ${
            pData[i].values[1]
          } `;
        }
      } else {
        const x = pData[0].values[0];
        const y = pData[0].values[1];
        d += `M ${x} ${y} `;
        d += this.#l2c(
          pData,
          { a: 1, b: 0, c: 0, d: 1, e: 0, f: 0 } as SVGMatrix,
          { a: 1, b: 0, c: 0, d: 1, e: 0, f: 0 } as SVGMatrix
        );
      }

      this.#stage.history?.add(this.#stage.history.actions().ADD, "手绘");
      const path = this.#createSvgNode("path") as SVGPathElement;
      path.setAttribute("d", d);
      path.setAttribute(
        "fill",
        this.#stage.options.fill || "rgb(216, 216, 216)"
      );
      path.setAttribute("stroke", this.#stage.options.stroke || "rgb(0, 0, 0)");
      this.#stage.svgRoot?.append(path);
      this.#stage.select?.set(path, { node: path });
      this.#stage.select?.resetFrame(true);
    }

    this.#shiftKey = false;
    this.clearPoints();
    this.#sTarget = void 0;
    this.#eTarget = void 0;
  };
  #convert2Path = (pData: Array<any>, CTM: SVGMatrix, cCTM: SVGMatrix) => {
    let d = "";
    const sx = CTM.a / cCTM.a;
    const sy = CTM.d / cCTM.d;
    const dx = (CTM.e - cCTM.e) / cCTM.a;
    const dy = (CTM.f - cCTM.f) / cCTM.d;
    pData.forEach((item: any) => {
      const x = (item.values[0] - dx) / sx;
      const y = (item.values[1] - dy) / sy;
      d += `L ${x}  ${y} `;
    });
    return d;
  };
  #l2c = (pData: Array<any>, CTM: SVGMatrix, cCTM: SVGMatrix) => {
    let d = "";
    let curpos = { x: pData[0].values[0], y: pData[0].values[1] };
    let pos_one = curpos;

    const sx = CTM.a / cCTM.a;
    const sy = CTM.d / cCTM.d;
    const dx = (CTM.e - cCTM.e) / cCTM.a;
    const dy = (CTM.f - cCTM.f) / cCTM.d;

    for (let i = 1; i <= pData.length - 2; i++) {
      const item = pData[i];
      const next = { x: item.values[0], y: item.values[1] };
      const next2 = {
        x: pData[i + 1].values[0],
        y: pData[i + 1].values[1],
      };
      const nps = this.#smoothControlPoints(curpos, next, next2) as any;
      const ct2 = nps[0];

      const x1 = (pos_one.x - dx) / sx;
      const y1 = (pos_one.y - dy) / sy;
      const x2 = (ct2.x - dx) / sx;
      const y2 = (ct2.y - dy) / sy;
      const x = (next.x - dx) / sx;
      const y = (next.y - dy) / sy;

      d += `C ${x1} ${y1} ${x2} ${y2} ${x} ${y} `;

      pos_one = nps[1];
      curpos = next;
    }
    return d;
  };

  #smoothControlPoints(
    ct1: { x: number; y: number },
    pt: { x: number; y: number },
    ct2: { x: number; y: number }
  ) {
    const x1 = ct1.x - pt.x,
      y1 = ct1.y - pt.y,
      x2 = ct2.x - pt.x,
      y2 = ct2.y - pt.y;

    let anglea = Math.atan2(y1, x1),
      angleb = Math.atan2(y2, x2);
    const r1 = Math.sqrt(x1 * x1 + y1 * y1);
    const r2 = Math.sqrt(x2 * x2 + y2 * y2);
    const nct1 = {
        x: 0,
        y: 0,
      },
      nct2 = {
        x: 0,
        y: 0,
      };
    if (anglea < 0) {
      anglea += 2 * Math.PI;
    }
    if (angleb < 0) {
      angleb += 2 * Math.PI;
    }

    const angleBetween = Math.abs(anglea - angleb),
      angleDiff = Math.abs(Math.PI - angleBetween) / 2;

    let new_anglea, new_angleb;
    if (anglea - angleb > 0) {
      new_anglea =
        angleBetween < Math.PI ? anglea + angleDiff : anglea - angleDiff;
      new_angleb =
        angleBetween < Math.PI ? angleb - angleDiff : angleb + angleDiff;
    } else {
      new_anglea =
        angleBetween < Math.PI ? anglea - angleDiff : anglea + angleDiff;
      new_angleb =
        angleBetween < Math.PI ? angleb + angleDiff : angleb - angleDiff;
    }

    // rotate the points
    nct1.x = (r1 * Math.cos(new_anglea)) / 3 + pt.x;
    nct1.y = (r1 * Math.sin(new_anglea)) / 3 + pt.y;
    nct2.x = (r2 * Math.cos(new_angleb)) / 3 + pt.x;
    nct2.y = (r2 * Math.sin(new_angleb)) / 3 + pt.y;

    return [nct1, nct2];
  }
  reset = () => {
    this.#pathPoint.clear();
    this.#freeHand.clear();
  };
  clearPoints = () => {
    this.#freehandLine?.points.clear();
  };
  clearGrippies = () => {
    if (this.#grippiesGroup) this.#grippiesGroup.innerHTML = "";
  };
  #createSvgNode(type: string) {
    return document.createElementNS("http://www.w3.org/2000/svg", type);
  }
  disabled = (isDisabled: boolean) => {
    this.#isDisabled = isDisabled;
    if (isDisabled) {
      this.reset();
      this.clearPoints();
      this.clearGrippies();
    } else {
      this.#initGrippies();
    }
  };
}
export default Freehand;
