import "./index.css";
import React from "react";
import { generateSVG } from "./blade";

class ThreeBox extends React.Component {
  createBlade = async () => {
    const stage = (window as any).stage;
    const l = document.querySelector("#length-input")?.getAttribute("value");
    const w = document.querySelector("#width-input")?.getAttribute("value");
    const h = document.querySelector("#height-input")?.getAttribute("value");
    const svg = (await generateSVG(
      Number(l),
      Number(w),
      Number(h),
      "B",
      null,
      null,
      true,
      true
    )) as any;
    if (stage.svgRoot && svg) {
      const oldBorder = stage.svgRoot?.querySelector("#边框");
      if (oldBorder) {
        const newBorder = svg.querySelector("#边框");
        stage.svgRoot.replaceChild(newBorder, oldBorder);
      } else {
        const childNodes = svg.childNodes;
        for (let i = 0; i < childNodes.length; i++) {
          stage.svgRoot.append(childNodes[i].cloneNode(true));
        }
      }
      const viewBox = svg?.viewBox.baseVal;
      stage?.setViewSize(viewBox.width, viewBox.height);
      const board = document.querySelector("#board");
      const { height: h } = board?.getBoundingClientRect() as DOMRect;
        stage.setBoardSize(Number(h - 40) / viewBox.height, 20, 20);
        stage.history.clear();
        stage.select.resetFrame(true);
    }
  };
  componentDidMount() {
    const l = document.querySelector("#length-input");
    const w = document.querySelector("#width-input");
    const h = document.querySelector("#height-input");

    l?.addEventListener("keydown", (ev) => {
      ev.stopPropagation();
    });
    w?.addEventListener("keydown", (ev) => {
      ev.stopPropagation();
    });
    h?.addEventListener("keydown", (ev) => {
      ev.stopPropagation();
    });

    const createBlade = document.querySelector("#create-blade");
    createBlade?.addEventListener("click", this.createBlade);
  }

  componentWillUnmount() {}

  render = () => {
    return (
      <div id="three-box">
        <header>
          <h2>
            <x-message>3D-BOX</x-message>
          </h2>
        </header>
        <section id="three-box-content" className="flex">
          <main id="three-box-main">
            <x-box id="x-box" vertical="">
              <h4 id="x-heading">
                <x-message>Length</x-message>
              </h4>
              <x-numberinput
                id="length-input"
                value="320"
                condensed=""
                role="input"
                aria-disabled="false"
                tabindex="0"
                hasstepper=""
                step="1"
              >
                <x-stepper></x-stepper>
              </x-numberinput>
            </x-box>
            <x-box id="x-box" vertical="">
              <h4 id="x-heading">
                <x-message>Width</x-message>
              </h4>
              <x-numberinput
                id="width-input"
                value="200"
                condensed=""
                role="input"
                aria-disabled="false"
                tabindex="0"
                hasstepper=""
                step="1"
              >
                <x-stepper></x-stepper>
              </x-numberinput>
            </x-box>
            <x-box id="x-box" vertical="">
              <h4 id="x-heading">
                <x-message>Height</x-message>
              </h4>
              <x-numberinput
                id="height-input"
                value="450"
                condensed=""
                role="input"
                aria-disabled="false"
                tabindex="0"
                hasstepper=""
                step="1"
              >
                <x-stepper></x-stepper>
              </x-numberinput>
            </x-box>

            <x-box id="blade-btn">
              <x-button
                toggled=""
                condensed=""
                role="button"
                aria-disabled="false"
                tabindex="0"
                skin="default"
                onClick={() => this.createBlade()}
              >
                CREATE BOX
              </x-button>
            </x-box>
          </main>
        </section>
      </div>
    );
  };
}

export default ThreeBox;
