const radius = 3;
// 缩放5倍
const mmUnit = 2.83464 / 5;

export interface ThreeDBaseModel {
  width: number; // 宽
  height: number; // 高
  z: number; // 厚度
  l: number; // 层数
  duration: number; //播放总时间
}

export interface SurfaceBaseModel {
  b: Array<string>;
  p: SursurfaceParamsListModel;
}

export type SursurfaceParamsListModel = SurfaceParamsModel[];

export interface SurfaceParamsModel {
  a: Array<number>; // 面的坐标 从左下角顺时针
  c: Array<number>; // 旋转
  t: Array<number>; // 动画时长
}

export interface ThreeDBoxModel {
  de: ThreeDBaseModel; // 3D基础数据
  ge: SurfaceBaseModel;
}

export const generate3DData = (
  l: number,
  w: number,
  h: number,
  d: number
): ThreeDBoxModel => {
  // 小纸尺寸长 毫米
  const bordWmm = d + 2 * l + 2 * w;
  // 小纸尺寸宽 毫米
  const bordHmm = h + w;
  const threeDBaseData = handleTreeDBase(bordWmm, bordHmm);
  // B D面折叠偏移
  const thickness = threeDBaseData.z * 1.5;
  // 普通箱
  const b = [
    "C",
    "B:C",
    "A:B",
    "D:C",
    "E:A",
    "SGB:B",
    "SGA:A",
    "SGC:C",
    "SGD:D",
    "XGB:B",
    "XGA:A",
    "XGC:C",
    "XGD:D"
  ];
  const x1 = 0;
  const x2 = x1 + d;
  const x3 = x2 + l;
  const x4 = x3 - radius;
  const x5 = x3 + radius;
  const x6 = x3 + w;
  const x7 = x6 - radius;
  const x8 = x6 + radius;
  const x9 = x6 + l;
  const x10 = x9 - radius;
  const x11 = x9 + radius;
  const x12 = x9 + w;

  const y1 = 0;
  const y2 = y1 + w / 2;
  const y3 = y2;
  const y4 = y2 + 5;
  const y5 = y2 + h;
  const y6 = y5 - 5;
  const y7 = y5;
  const y8 = y5 + w / 2;
  const p: SursurfaceParamsListModel = [];
  // C面
  addSurfaceParams(
    p,
    [
      x6 * mmUnit,
      y2 * mmUnit,
      x6 * mmUnit,
      y5 * mmUnit,
      x9 * mmUnit,
      y5 * mmUnit,
      x9 * mmUnit,
      y2 * mmUnit
    ],
    [0, 90],
    [0, 3]
  );
  // B面
  addSurfaceParams(
    p,
    [
      x3 * mmUnit,
      y2 * mmUnit,
      x3 * mmUnit,
      (y5 - thickness) * mmUnit,
      x6 * mmUnit,
      (y5 - thickness) * mmUnit,
      x6 * mmUnit,
      (y2 + thickness) * mmUnit
    ],
    [0, 90],
    [0, 3]
  );
  // A面
  addSurfaceParams(
    p,
    [
      x2 * mmUnit,
      y2 * mmUnit,
      x2 * mmUnit,
      y5 * mmUnit,
      x3 * mmUnit,
      y5 * mmUnit,
      x3 * mmUnit,
      y2 * mmUnit
    ],
    [0, 90],
    [0, 3]
  );
  // D面
  addSurfaceParams(
    p,
    [
      x9 * mmUnit,
      y2 * mmUnit,
      x9 * mmUnit,
      (y5 - thickness) * mmUnit,
      x12 * mmUnit,
      (y5 - thickness) * mmUnit,
      x12 * mmUnit,
      (y2 + thickness) * mmUnit
    ],
    [0, 90],
    [0, 3]
  );
  // E面
  addSurfaceParams(
    p,
    [
      x1 * mmUnit,
      y4 * mmUnit,
      x1 * mmUnit,
      y6 * mmUnit,
      x2 * mmUnit,
      y5 * mmUnit,
      x2 * mmUnit,
      y2 * mmUnit
    ],
    [0, 91],
    [0, 3]
  );
  // SGB:B
  addSurfaceParams(
    p,
    [
      x5 * mmUnit,
      (y5 - thickness) * mmUnit,
      x5 * mmUnit,
      y8 * mmUnit,
      x7 * mmUnit,
      y8 * mmUnit,
      x7 * mmUnit,
      (y5 - thickness) * mmUnit
    ],
    [0, 95],
    [3, 6]
  );
  // SGA:A
  addSurfaceParams(
    p,
    [
      x2 * mmUnit,
      y5 * mmUnit,
      x2 * mmUnit,
      y8 * mmUnit,
      x4 * mmUnit,
      y8 * mmUnit,
      x4 * mmUnit,
      y5 * mmUnit
    ],
    [0, 90],
    [6, 9]
  );
  // SGC:C
  addSurfaceParams(
    p,
    [
      x8 * mmUnit,
      y5 * mmUnit,
      x8 * mmUnit,
      y8 * mmUnit,
      x10 * mmUnit,
      y8 * mmUnit,
      x10 * mmUnit,
      y5 * mmUnit
    ],
    [0, 90],
    [6, 9]
  );
  // SGD:D
  addSurfaceParams(
    p,
    [
      x11 * mmUnit,
      (y5 - thickness) * mmUnit,
      x11 * mmUnit,
      y8 * mmUnit,
      x12 * mmUnit,
      y8 * mmUnit,
      x12 * mmUnit,
      (y5 - thickness) * mmUnit
    ],
    [0, 95],
    [3, 6]
  );
  // XGB:B
  addSurfaceParams(
    p,
    [
      x5 * mmUnit,
      y1 * mmUnit,
      x5 * mmUnit,
      (y2 + thickness) * mmUnit,
      x7 * mmUnit,
      (y2 + thickness) * mmUnit,
      x7 * mmUnit,
      y1 * mmUnit
    ],
    [0, 95],
    [3, 6]
  );
  // XGA:A
  addSurfaceParams(
    p,
    [
      x2 * mmUnit,
      y1 * mmUnit,
      x2 * mmUnit,
      y2 * mmUnit,
      x4 * mmUnit,
      y2 * mmUnit,
      x4 * mmUnit,
      y1 * mmUnit
    ],
    [0, 90],
    [6, 9]
  );
  // XGC:C
  addSurfaceParams(
    p,
    [
      x8 * mmUnit,
      y1 * mmUnit,
      x8 * mmUnit,
      y2 * mmUnit,
      x10 * mmUnit,
      y2 * mmUnit,
      x10 * mmUnit,
      y1 * mmUnit
    ],
    [0, 90],
    [6, 9]
  );
  // XGD:D
  addSurfaceParams(
    p,
    [
      x11 * mmUnit,
      y1 * mmUnit,
      x11 * mmUnit,
      (y2 + thickness) * mmUnit,
      x12 * mmUnit,
      (y2 + thickness) * mmUnit,
      x12 * mmUnit,
      y1 * mmUnit
    ],
    [0, 95],
    [3, 6]
  );
  const surfaceBaseData: SurfaceBaseModel = { b, p };
  return {
    de: threeDBaseData,
    ge: surfaceBaseData
  };
};

// 获取3D基础数据
const handleTreeDBase = (bordWmm: number, bordHmm: number): ThreeDBaseModel => {
  const threeDBaseData = {
    width: bordWmm * mmUnit,
    height: bordHmm * mmUnit,
    z: 3 * mmUnit,
    l: 3, // 层数
    duration: 9 //播放总时间
  };
  return threeDBaseData;
};

const addSurfaceParams = (
  p: SursurfaceParamsListModel,
  a: Array<number>,
  c: Array<number>,
  t: Array<number>
) => {
  const surfaceParamsModel: SurfaceParamsModel = { a, c, t };
  p.push(surfaceParamsModel);
};
