// 读取配置项
export const readConfig = async () => {
  return designCofigDeafault;
};
interface cortypeType {
  cortype: string;
  connectlen: number;
  coverlen: number;
  round: boolean;
  attributeState: boolean;
}
// 画刀模图
export const generateSVG = async (
  l: number,
  w: number,
  h: number,
  cortype: string,
  svgTemplate: Element | null,
  selItem: any,
  isGenerateContent: boolean,
  showBorder?: boolean
) => {
  // 读取配置项
  const designCofigObject = (await readConfig()) as any;
    const cartonParamsList: cortypeType[] = JSON.parse(designCofigObject).cartonParams;


  const cartonParams = cartonParamsList.find(
    (d: cortypeType) => cortype === d.cortype
  );
  // let cartonParams;
  // for (let cartonItem of cartonParamsList) {
  //   if (cartonItem.cortype === cortype) {
  //     cartonParams = cartonItem;
  //     break;
  //   }
  // }
  if (!cartonParams) {
    return `没有${cortype}该箱型楞型参数`;
  }

  const cover = Math.floor(Number(cartonParams.coverlen) / 2);
  // 钉条长度
  const d = Number(cartonParams.connectlen) + cover;
  // 小纸尺寸宽 毫米
  const bordWmm = Number(cartonParams.connectlen) + cover + 2 * l + 2 * w;
  // 小纸尺寸宽 毫米
  const bordHmm = h + Math.floor((w + Number(cartonParams.coverlen)) / 2) * 2;
  // 像素
  const bordW = bordWmm * mmUnit;
  const bordH = bordHmm * mmUnit;
  // 重新设置画板大小
  // stage.setViewSize(
  //   bordWmm * mmUnit,
  //   bordHmm * mmUnit,
  // );

  // 获取SVG版本号

  const data_version = svgTemplate?.getAttribute("data-version");
  // 创建sgv外层元素
  let svg = document.createElementNS("http://www.w3.org/2000/svg", "svg");
  // 设置创建的svg属性
  svg.setAttribute("viewBox", `0 0 ${bordW} ${bordH}`);
  svg.setAttribute("xmlns", "http://www.w3.org/2000/svg");
  svg.setAttribute("xmlns:xlink", "http://www.w3.org/1999/xlink");
  // svg.setAttribute("width", `${bordW}`);
  // svg.setAttribute("height", `${bordH}`);
  data_version && svg.setAttribute("data-version", data_version);

  const x1 = 0;
  const x2 = x1 + d;
  const x3 = x2 + l;
  const x4 = x3 - radius;
  const x5 = x3 + radius;
  const x6 = x3 + w;
  const x7 = x6 - radius;
  const x8 = x6 + radius;
  const x9 = x6 + l;
  const x10 = x9 - radius;
  const x11 = x9 + radius;
  const x12 = x9 + w - cover;
  // 摇盖高
  const cover_h = Math.floor((w + Number(cartonParams.coverlen)) / 2);
  const y1 = 0;
  const y2 = y1 + cover_h;
  const y3 = y2 + h;
  const y4 = y3 + cover_h;
  const gFrame = new SVGGroup({
    id: "边框",
    pointerEvents: "none",
    display: showBorder ? "unset" : "none",
  });

  gFrame.add(
    new SVGRect({
      id: "AT",
      x: x2 * mmUnit,
      y: y1 * mmUnit,
      width: (x4 - x2) * mmUnit,
      height: (y2 - y1) * mmUnit,
      fill: "none",
      stroke: "black",
      strokeWidth: rectStrokeWidth,
    })
  );
  gFrame.add(
    new SVGRect({
      id: "AB",
      x: x2 * mmUnit,
      y: y3 * mmUnit,
      width: (x4 - x2) * mmUnit,
      height: (y4 - y3) * mmUnit,
      fill: "none",
      stroke: "black",
      strokeWidth: rectStrokeWidth,
    })
  );
  gFrame.add(
    new SVGRect({
      id: "BT",
      x: x5 * mmUnit,
      y: y1 * mmUnit,
      width: (x7 - x5) * mmUnit,
      height: (y2 - y1) * mmUnit,
      fill: "none",
      stroke: "black",
      strokeWidth: rectStrokeWidth,
    })
  );
  gFrame.add(
    new SVGRect({
      id: "BB",
      x: x5 * mmUnit,
      y: y3 * mmUnit,
      width: (x7 - x5) * mmUnit,
      height: (y4 - y3) * mmUnit,
      fill: "none",
      stroke: "black",
      strokeWidth: rectStrokeWidth,
    })
  );
  gFrame.add(
    new SVGRect({
      id: "CT",
      x: x8 * mmUnit,
      y: y1 * mmUnit,
      width: (x10 - x8) * mmUnit,
      height: (y2 - y1) * mmUnit,
      fill: "none",
      stroke: "black",
      strokeWidth: rectStrokeWidth,
    })
  );
  gFrame.add(
    new SVGRect({
      id: "CB",
      x: x8 * mmUnit,
      y: y3 * mmUnit,
      width: (x10 - x8) * mmUnit,
      height: (y4 - y3) * mmUnit,
      fill: "none",
      stroke: "black",
      strokeWidth: rectStrokeWidth,
    })
  );
  gFrame.add(
    new SVGRect({
      id: "DT",
      x: x11 * mmUnit,
      y: y1 * mmUnit,
      width: (x12 - x11) * mmUnit,
      height: (y2 - y1) * mmUnit,
      fill: "none",
      stroke: "black",
      strokeWidth: rectStrokeWidth,
    })
  );
  gFrame.add(
    new SVGRect({
      id: "DB",
      x: x11 * mmUnit,
      y: y3 * mmUnit,
      width: (x12 - x11) * mmUnit,
      height: (y4 - y3) * mmUnit,
      fill: "none",
      stroke: "black",
      strokeWidth: rectStrokeWidth,
    })
  );
  gFrame.add(
    new SVGRect({
      id: "E",
      x: x1 * mmUnit,
      y: y2 * mmUnit,
      width: (x2 - x1) * mmUnit,
      height: (y3 - y2) * mmUnit,
      fill: "none",
      stroke: "black",
      strokeWidth: rectStrokeWidth,
    })
  );
  gFrame.add(
    new SVGRect({
      id: "A",
      x: x2 * mmUnit,
      y: y2 * mmUnit,
      width: (x3 - x2) * mmUnit,
      height: (y3 - y2) * mmUnit,
      fill: "none",
      stroke: "red",
      strokeWidth: rectStrokeWidth,
    })
  );
  gFrame.add(
    new SVGRect({
      id: "B",
      x: x3 * mmUnit,
      y: y2 * mmUnit,
      width: (x6 - x3) * mmUnit,
      height: (y3 - y2) * mmUnit,
      fill: "none",
      stroke: "red",
      strokeWidth: rectStrokeWidth,
    })
  );
  gFrame.add(
    new SVGRect({
      id: "C",
      x: x6 * mmUnit,
      y: y2 * mmUnit,
      width: (x9 - x6) * mmUnit,
      height: (y3 - y2) * mmUnit,
      fill: "none",
      stroke: "red",
      strokeWidth: rectStrokeWidth,
    })
  );
  gFrame.add(
    new SVGRect({
      id: "D",
      x: x9 * mmUnit,
      y: y2 * mmUnit,
      width: (x12 - x9) * mmUnit,
      height: (y3 - y2) * mmUnit,
      fill: "none",
      stroke: "red",
      strokeWidth: rectStrokeWidth,
    })
  );

  const y7 = y1 - 30;
  const y8 = y7 - 5;
  const y9 = y7 + 5;
  const y10 = y7 - 6;
  const y11 = y7 - 30;
  const y12 = y11 - 5;
  const y13 = y11 + 5;
  const y14 = y11 - 6;

  const x13 = x12 + 30;
  const x14 = x13 - 5;
  const x15 = x13 + 5;
  const x16 = x13 + 15;
  const x17 = x13 + 50;
  const x18 = x17 - 5;
  const x19 = x17 + 5;
  const x20 = x17 + 15;
  // E 长
  addMeasureLine(
    gFrame,
    mmUnit,
    x1,
    y9,
    x1,
    y8,
    x1,
    y7,
    x2,
    y7,
    x2,
    y9,
    x2,
    y8,
    x2 / 2,
    y10,
    x2 - x1 + ""
  );
  // A 长
  addMeasureLine(
    gFrame,
    mmUnit,
    x2,
    y9,
    x2,
    y8,
    x2,
    y7,
    x3,
    y7,
    x3,
    y9,
    x3,
    y8,
    (x3 - x2) / 2 + x2,
    y10,
    x3 - x2 + ""
  );
  // B 长
  addMeasureLine(
    gFrame,
    mmUnit,
    x3,
    y9,
    x3,
    y8,
    x3,
    y7,
    x6,
    y7,
    x6,
    y9,
    x6,
    y8,
    (x6 - x3) / 2 + x3,
    y10,
    x6 - x3 + ""
  );
  // C 长
  addMeasureLine(
    gFrame,
    mmUnit,
    x6,
    y9,
    x6,
    y8,
    x6,
    y7,
    x9,
    y7,
    x9,
    y9,
    x9,
    y8,
    (x9 - x6) / 2 + x6,
    y10,
    x9 - x6 + ""
  );
  // D 长
  addMeasureLine(
    gFrame,
    mmUnit,
    x9,
    y9,
    x9,
    y8,
    x9,
    y7,
    x12,
    y7,
    x12,
    y9,
    x12,
    y8,
    (x12 - x9) / 2 + x9,
    y10,
    x12 - x9 + ""
  );
  // A-D 长
  addMeasureLine(
    gFrame,
    mmUnit,
    x1,
    y13,
    x1,
    y12,
    x1,
    y11,
    x12,
    y11,
    x12,
    y13,
    x12,
    y12,
    (x12 - x1) / 2,
    y14,
    x12 - x1 + ""
  );
  // DXG 高
  addMeasureLine(
    gFrame,
    mmUnit,
    x14,
    y1,
    x15,
    y1,
    x13,
    y1,
    x13,
    y2,
    x14,
    y2,
    x15,
    y2,
    x16,
    (y2 - y1) / 2 + y1,
    y2 - y1 + ""
  );
  // D 高
  addMeasureLine(
    gFrame,
    mmUnit,
    x14,
    y2,
    x15,
    y2,
    x13,
    y2,
    x13,
    y4,
    x14,
    y4,
    x15,
    y4,
    x16,
    (y3 - y2) / 2 + y2,
    y3 - y2 + ""
  );
  // DSG 高
  addMeasureLine(
    gFrame,
    mmUnit,
    x14,
    y3,
    x15,
    y3,
    x13,
    y3,
    x13,
    y4,
    x14,
    y4,
    x15,
    y4,
    x16,
    (y4 - y3) / 2 + y3,
    y4 - y3 + ""
  );
  // X-SG 高
  addMeasureLine(
    gFrame,
    mmUnit,
    x18,
    y1,
    x19,
    y1,
    x17,
    y1,
    x17,
    y4,
    x18,
    y4,
    x19,
    y4,
    x20,
    (y4 - y1) / 2 + y1,
    y4 - y1 + ""
  );
  const gEditor = new SVGGroup({ id: "编辑" });
  // 处理钉条内容，如果有订单编号和订货数量字段
  if (selItem && (selItem["订单编号"] || selItem["订货数量"])) {
    let x = x1 * mmUnit + ((x2 - x1) * mmUnit) / 2;
    let y = y2 * mmUnit + ((y3 - y2) * mmUnit) / 2;
    let DingText = new SVGText({
      id: "钉条",
      fontSize: "30px",
      fontFamily: "'SimHei','黑体'",
      textAnchor: "middle",
      content: `${selItem["订单编号"]}${
        selItem["订单编号"] && selItem["订货数量"]
          ? "-" + selItem["订货数量"]
          : selItem["订货数量"]
      }`,
    });
    DingText.node.setAttribute("transform", `matrix(0, -1, 1, 0, ${x},  ${y})`);
    gEditor.add(DingText);
    // 删除订单编号和订货数量
    delete selItem["订单编号"];
    delete selItem["订货数量"];
  }
  if (isGenerateContent) {
    // 判断是否 生成模板
    const surfacePositionMap = hanlderPTXPosition(l, w, h, d, cover, cover_h);
    hanlderSVGModel(
      surfacePositionMap,
      gEditor.node,
      gFrame.node,
      svgTemplate,
      selItem
    );
  }
  // 把画板根节点的子节点添加到创建的svg元素里面
  svg.innerHTML = gFrame.node.outerHTML + gEditor.node.outerHTML;
  return svg;
};

//处理模板移动和替换变量
const hanlderSVGModel = (
  surfacePositionMap: Map<string, object>,
  gEditor: any,
  gFrame: any,
  svgTemplate: Element | null,
  selItem: any
) => {
  // 替换变量
  const getTextTags = svgTemplate?.getElementsByTagName("text") || [];
  for (const key in selItem) {
    for (let i = 0; i < getTextTags.length; i++) {
      let textId = getTextTags[i].getAttribute("id");
      if (!textId) {
        continue;
      }

      if (textId === key) {
        // 处理/n 换行
        let content = selItem[key].toString();
        let fontSize = getTextTags[i].getAttribute("font-size");
        if (fontSize && content && content.indexOf("/n") !== -1) {
          content = content.replace(
            RegExp("/n", "g"),
            `<tspan x="0" dy="${fontSize}">\u200B</tspan>`
          );
        }
        // const replaceContent = getTextTags[i].innerHTML.replace(/\S/g, content);
        getTextTags[i].innerHTML = content;
      }
    }
  }
  const getGTags = svgTemplate?.getElementsByTagName("g") || [];
  for (let i = 0; i < getGTags.length; i++) {
    const gTag = getGTags[i];
    const area = gTag.dataset.area;
    const pt = gTag.dataset.pt;
    const dataAreaRep = area;
    if (!area || !pt) {
      continue;
    }
    const x = gTag.dataset.x || 0;
    const y = gTag.dataset.y || 0;
    const surfacePosition = surfacePositionMap.get(dataAreaRep + "-" + pt);
    if (surfacePosition) {
      // 移动模板到对应位置
      moveTemplate(surfacePosition, gTag, pt, Number(x), Number(y));
      if (gTag.hasAttribute("data-join-blade")) {
        gFrame.innerHTML = gFrame.innerHTML + gTag.outerHTML;
      } else {
        gEditor.innerHTML = gEditor.innerHTML + gTag.outerHTML;
      }
    }
  }
};
// 移动模板到对应位置
const moveTemplate = (
  surfacePosition: any,
  gtag: any,
  dataPt: string,
  dataX: number,
  dataY: number
) => {
  let x = surfacePosition.x;
  let y = surfacePosition.y;
  const offsetX = dataX;
  const offsetY = dataY;
  if (dataPt.indexOf("T") != -1) {
    y = y + offsetY;
  }
  if (dataPt.indexOf("B") != -1) {
    y = y - offsetY;
  }
  if (dataPt.indexOf("L") != -1) {
    x = x + offsetX;
  }
  if (dataPt.indexOf("R") != -1) {
    x = x - offsetX;
  }
  gtag.setAttribute(
    "transform",
    `matrix(1, 0, 0, 1, ${x * mmUnit},  ${y * mmUnit})`
  );
};

// 处理普通箱各个面坐标
const hanlderPTXPosition = (
  l: number,
  w: number,
  h: number,
  d: number,
  cover: number,
  cover_h: number
): Map<string, object> => {
  let originX = 0,
    x1 = 0,
    x2 = 0,
    x3 = 0,
    x4 = 0,
    x5 = 0,
    x6 = 0,
    x7 = 0,
    x8 = 0,
    x9 = 0,
    x10 = 0;
  let originY = 0,
    y1 = 0,
    y2 = 0,
    y3 = 0,
    y4 = 0,
    y5 = 0,
    y6 = 0;
  x1 = d / 2;
  x2 = d;
  x3 = x2 + l / 2;
  x4 = x2 + l;
  x5 = x4 + w / 2;
  x6 = x4 + w;
  x7 = x6 + l / 2;
  x8 = x6 + l;
  x9 = x8 + (w - cover) / 2;
  x10 = x8 + (w - cover);
  y1 = cover_h / 2;
  y2 = cover_h;
  y3 = y2 + h / 2;
  y4 = y2 + h;
  y5 = y4 + cover_h / 2;
  y6 = y4 + cover_h;
  const surfacePositionHashMap = new Map<string, object>();
  // 钉条
  addSurfacePositionList(surfacePositionHashMap, "E-C", x1, y3);
  // A面左
  addSurfaceLeftAndRightPosition(
    surfacePositionHashMap,
    "A",
    "L",
    x2,
    originY,
    y1,
    y2,
    y3,
    y4,
    y5,
    y6
  );
  // A面中心
  addSurfaceCenterPosition(
    surfacePositionHashMap,
    "A",
    x3,
    originY,
    y1,
    y2,
    y3,
    y4,
    y5,
    y6
  );
  // A面右
  addSurfaceLeftAndRightPosition(
    surfacePositionHashMap,
    "A",
    "R",
    x4,
    originY,
    y1,
    y2,
    y3,
    y4,
    y5,
    y6
  );
  // B面左
  addSurfaceLeftAndRightPosition(
    surfacePositionHashMap,
    "B",
    "L",
    x4,
    originY,
    y1,
    y2,
    y3,
    y4,
    y5,
    y6
  );
  // B面中心
  addSurfaceCenterPosition(
    surfacePositionHashMap,
    "B",
    x5,
    originY,
    y1,
    y2,
    y3,
    y4,
    y5,
    y6
  );
  // B面右
  addSurfaceLeftAndRightPosition(
    surfacePositionHashMap,
    "B",
    "R",
    x6,
    originY,
    y1,
    y2,
    y3,
    y4,
    y5,
    y6
  );
  // C面左
  addSurfaceLeftAndRightPosition(
    surfacePositionHashMap,
    "C",
    "L",
    x6,
    originY,
    y1,
    y2,
    y3,
    y4,
    y5,
    y6
  );
  // C面中心
  addSurfaceCenterPosition(
    surfacePositionHashMap,
    "C",
    x7,
    originY,
    y1,
    y2,
    y3,
    y4,
    y5,
    y6
  );
  // C面右
  addSurfaceLeftAndRightPosition(
    surfacePositionHashMap,
    "C",
    "R",
    x8,
    originY,
    y1,
    y2,
    y3,
    y4,
    y5,
    y6
  );
  // D面左
  addSurfaceLeftAndRightPosition(
    surfacePositionHashMap,
    "D",
    "L",
    x8,
    originY,
    y1,
    y2,
    y3,
    y4,
    y5,
    y6
  );
  // D面中心
  addSurfaceCenterPosition(
    surfacePositionHashMap,
    "D",
    x9,
    originY,
    y1,
    y2,
    y3,
    y4,
    y5,
    y6
  );
  // D面右
  addSurfaceLeftAndRightPosition(
    surfacePositionHashMap,
    "D",
    "R",
    x10,
    originY,
    y1,
    y2,
    y3,
    y4,
    y5,
    y6
  );
  return surfacePositionHashMap;
};
// 添加 上 中 下 面 居中坐标
const addSurfaceCenterPosition = (
  surfacePositionMap: Map<string, object>,
  surfaceName: string,
  x: number,
  originY: number,
  y1: number,
  y2: number,
  y3: number,
  y4: number,
  y5: number,
  y6: number
) => {
  addSurfacePositionList(surfacePositionMap, surfaceName + "T-TC", x, originY);
  addSurfacePositionList(surfacePositionMap, surfaceName + "T-C", x, y1);
  addSurfacePositionList(surfacePositionMap, surfaceName + "T-BC", x, y2);
  addSurfacePositionList(surfacePositionMap, surfaceName + "-TC", x, y2);
  addSurfacePositionList(surfacePositionMap, surfaceName + "-C", x, y3);
  addSurfacePositionList(surfacePositionMap, surfaceName + "-BC", x, y4);
  addSurfacePositionList(surfacePositionMap, surfaceName + "B-TC", x, y4);
  addSurfacePositionList(surfacePositionMap, surfaceName + "B-C", x, y5);
  addSurfacePositionList(surfacePositionMap, surfaceName + "B-BC", x, y6);
};

// 添加 上 中 下 面 左右坐标
const addSurfaceLeftAndRightPosition = (
  surfacePositionMap: Map<string, object>,
  surfaceName: string,
  pt: string,
  x: number,
  originY: number,
  y1: number,
  y2: number,
  y3: number,
  y4: number,
  y5: number,
  y6: number
) => {
  addSurfacePositionList(
    surfacePositionMap,
    surfaceName + "T-" + pt + "T",
    x,
    originY
  );
  addSurfacePositionList(
    surfacePositionMap,
    surfaceName + "T-" + pt + "C",
    x,
    y1
  );
  addSurfacePositionList(
    surfacePositionMap,
    surfaceName + "T-" + pt + "B",
    x,
    y2
  );
  addSurfacePositionList(
    surfacePositionMap,
    surfaceName + "-" + pt + "T",
    x,
    y2
  );
  addSurfacePositionList(
    surfacePositionMap,
    surfaceName + "-" + pt + "C",
    x,
    y3
  );
  addSurfacePositionList(
    surfacePositionMap,
    surfaceName + "-" + pt + "B",
    x,
    y4
  );
  addSurfacePositionList(
    surfacePositionMap,
    surfaceName + "B-" + pt + "T",
    x,
    y4
  );
  addSurfacePositionList(
    surfacePositionMap,
    surfaceName + "B-" + pt + "C",
    x,
    y5
  );
  addSurfacePositionList(
    surfacePositionMap,
    surfaceName + "B-" + pt + "B",
    x,
    y6
  );
};

// 添加各个面坐标
const addSurfacePositionList = (
  map: Map<string, object>,
  name: string,
  x: number,
  y: number
) => {
  map.set(name, { x, y });
};
//画尺寸
const addMeasureLine = (
  gFrame: any,
  mmUnit: number,
  lineLX1: number,
  lineLY1: number,
  lineLX2: number,
  lineLY2: number,
  lineCX1: number,
  lineCY1: number,
  lineCX2: number,
  lineCY2: number,
  lineRX1: number,
  lineRY1: number,
  lineRX2: number,
  lineRY2: number,
  textX: number,
  textY: number,
  content: string
) => {
  //左竖线
  const leftLine = new SVGLine({
    fill: "green",
    stroke: "green",
  });
  leftLine.node.setAttribute("x1", lineLX1 * mmUnit + "");
  leftLine.node.setAttribute("y1", lineLY1 * mmUnit + "");
  leftLine.node.setAttribute("x2", lineLX2 * mmUnit + "");
  leftLine.node.setAttribute("y2", lineLY2 * mmUnit + "");
  gFrame.add(leftLine);
  //中横线
  const centerLine = new SVGLine({
    fill: "green",
    stroke: "green",
  });
  centerLine.node.setAttribute("x1", lineCX1 * mmUnit + "");
  centerLine.node.setAttribute("y1", lineCY1 * mmUnit + "");
  centerLine.node.setAttribute("x2", lineCX2 * mmUnit + "");
  centerLine.node.setAttribute("y2", lineCY2 * mmUnit + "");
  gFrame.add(centerLine);
  //右竖线
  const rightLine = new SVGLine({
    fill: "green",
    stroke: "green",
  });
  rightLine.node.setAttribute("x1", lineRX1 * mmUnit + "");
  rightLine.node.setAttribute("y1", lineRY1 * mmUnit + "");
  rightLine.node.setAttribute("x2", lineRX2 * mmUnit + "");
  rightLine.node.setAttribute("y2", lineRY2 * mmUnit + "");
  gFrame.add(rightLine);
  //文本
//   gFrame.add(
//     new SVGText({
//       x: textX * mmUnit,
//       y: textY * mmUnit,
//       x2: lineRX2 * mmUnit,
//       y2: lineRY2 * mmUnit,
//       fill: "green",
//       fontFamily: "'SimHei','黑体'",
//       fontSize: "50px",
//       textAnchor: "middle",
//       content: content,
//     })
//   );
};
