import "./index.css";
import React from "react";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
// import skin_img from "../../assets/three-preview/skin.png"; //楞的皮肤
// import texture_img from "../../assets/three-preview/wa_B.jpg"; //默认牛皮纸
import playPng from "../../assets/three-preview/play.png"; //播放图片
import pausePng from "../../assets/three-preview/pause.png"; //暂停图片

const skin_img = 'https://darkdragonblade.github.io/svgeditor/build/three-preview/skin.png';
const texture_img = 'https://darkdragonblade.github.io/svgeditor/build/three-preview/wa_B.jpg';

interface State {
  time: number;
  threeDom: any;
  paused: Boolean;
  changeFlag: Boolean;
  maxDuration: number;
}

interface Props {}

// const threeDom: Ref = ref();
// let paused: Ref<boolean> = ref(false);
// let changeFlag: Ref<boolean> = ref(false);
let camera: any; //相机
let cameraPosition: Array<number> = [0, 0, 1800]; //相机位置
let renderer: any; //渲染器
let controls: any; //运动器
let ambientLight: any; //环境光
let sunLight: any; //跟随光，点光源
let scene: any; //场景
let stopAnimation: any; //轨道运动器
let stopAnimationAction: any; //动画播放器
let object: any; //模型对象
let sideColor = 0xf1f1f1; //默认皮纸颜色
let mixer: any; //裁剪器
let AnimationAction: any; //动画对象
let cubeGroup: Array<any> = [];
let canvasMaterial: any; //贴图CANVAS
let backMaterial: any; //背面贴图
let skinMaterial: any; //纹理皮肤
let origin = {
  minX: 0,
  maxX: 0,
  minY: 0,
  maxY: 0,
}; //初始点对象
// let time: Ref<number> = ref(0); //播放得时间节点
// let maxDuration: Ref<number> = ref(0); //最大播放时长
let clock: any;
let ge: any; //建模坐标数据
let de: any; //基础图形数据
let totalWidth: number; //模型总长度
let totalHeight: number; //模型总宽度
let textureImg: string; //皮肤材质
let ambientLightIntensity = 0.9; //环境光强度
let sunLightIntensity = 0.45; //阳光强度
let pointRadius = 0; //点光源弧度
let ground: any; //地面
let grid: any; //地面网格

class THREE_PREVIEW extends React.Component {
  public state: State;
  constructor(props: Props) {
    super(props);
    this.state = {
      time: 0,
      threeDom: void 0,
      paused: false,
      changeFlag: false,
      maxDuration: 0,
    };
  }

  changeMaterial = (url: string) => {
    textureImg = url;
    this.updateTexture();
  };

  cameraChange = (cameraData: any) => {
    camera.position.set(...cameraData);
    camera.rotation.set(0, 0, 0);
    camera.updateProjectionMatrix();
    AnimationAction.time = this.state.maxDuration;
  };

  changeAmbientLightIntensity = (intensity: number) => {
    ambientLight.intensity = intensity;
  };

  changeSunIntensity = (intensity: number) => {
    sunLight.intensity = intensity;
  };

  changeSunAngle = (radius: number) => {
    pointRadius = radius;
    this.changeShadowAngle();
  };

  changeBackground = (result: string) => {
    scene.background = this.loadTexture(result);
  };

  //切换预览区域大小
  switchScreen = () => {
    if (camera) {
      const screenWidth = window.innerWidth;
      const screenHeight = window.innerHeight;
      camera.aspect = screenWidth / screenHeight;
      camera.updateProjectionMatrix();
      renderer.setSize(screenWidth, screenHeight);
      const threeDom = document.querySelector(".three-dom") as HTMLDivElement;
      threeDom.removeChild(renderer.domElement);
      threeDom.appendChild(renderer.domElement);
    }
  };

  //更新贴图
  updateTexture = async () => {
    const canvas = (await this.createCanvas()) as HTMLImageElement;
    canvasMaterial.map = new THREE.CanvasTexture(canvas);
  };

  //相机初始化
  initPerspectiveCamera = () => {
    const screenWidth = window.innerWidth;
    const screenHeight = window.innerHeight;
    let fov = 25;
    let aspect = screenWidth / screenHeight; // 防止变形
    let near = 10; //和相机的透视距离
    let far = 100000;
    let lookAt = [0, 0, 0];
    camera = new THREE.PerspectiveCamera(fov, aspect, near, far);
    //设置相机的视点
    camera.position.set(...cameraPosition);
    //设置相机的朝向
    camera.lookAt(new THREE.Vector3(...lookAt));
  };

  //初始化渲染
  initRender = () => {
    const screenWidth = window.innerWidth;
    const screenHeight = window.innerHeight;
    renderer = new THREE.WebGLRenderer({
      antialias: true,
      alpha: true,
      precision: "highp",
      preserveDrawingBuffer: true,
      // logarithmicDepthBuffer: true,
    });

    renderer.setPixelRatio(window.devicePixelRatio);
    //设置渲染器的高度和宽度，如果加上第三个值 false，则按场景大小显示，等比例缩放
    renderer.setSize(screenWidth, screenHeight);
    //告诉渲染器需要阴影效果
    renderer.shadowMap.enabled = true;
    // 默认的是，没有设置的这个清晰 THREE.PCFShadowMap
    renderer.shadowMap.type = THREE.PCFSoftShadowMap;
    // 物体的渲染顺序将会由他们添加到场景中的顺序所决定
    renderer.sortObjects = true;
    //将渲染器添加到html当中
    const threeDom = document.querySelector(".three-dom") as HTMLDivElement;
    threeDom.appendChild(renderer.domElement);
  };

  //轨道旋转器
  initControls = () => {
    controls = new OrbitControls(camera, renderer.domElement);
  };
  //阳光设置
  setSunLight = () => {
    if (sunLight) this.removeScene(sunLight);

    sunLight = new THREE.DirectionalLight(0xffffff, sunLightIntensity);
    var z = (totalWidth / 2) * Math.cos(-pointRadius / 57.2);
    var x = (totalWidth / 2) * Math.sin(-pointRadius / 57.2);
    sunLight.position.set(x, totalHeight, z);
    scene.add(sunLight);

    sunLight.castShadow = true;

    sunLight.shadow.mapSize.width = 3000;
    sunLight.shadow.mapSize.height = 3000;

    var d = 2500;

    sunLight.shadow.camera.left = -d;
    sunLight.shadow.camera.right = d;
    sunLight.shadow.camera.top = d;
    sunLight.shadow.camera.bottom = -d;

    // sunLight.shadow.camera.fov = 500;
    sunLight.shadow.camera.far = 2500;
    sunLight.shadow.bias = -0.0001;
  };
  //切换平行光/阳光角度
  changeShadowAngle = () => {
    var z = (totalWidth / 2) * Math.cos(-pointRadius / 57.2);
    var x = (totalWidth / 2) * Math.sin(-pointRadius / 57.2);
    sunLight.position.set(x, totalHeight, z);
  };
  /**创建环境光
   * 环境光会照亮场景中所有的物体，在计算物体的颜色的时候，都会叠加上环境光的颜色
   * 由于环境光作用于所有的物体，所有的材质，所以环境光是没有方向的，也无法产生阴影效果
   * @param {*} color 光颜色
   * @param {*} intensity 光强度
   */
  initAmbientLight = () => {
    if (ambientLight) this.removeScene(ambientLight);
    ambientLight = new THREE.AmbientLight("#ffffff", ambientLightIntensity); // 默认创建一个灰色的环境光
    this.addScene(ambientLight);
  };
  //初始化光源
  initLight = () => {
    this.initAmbientLight();
    this.setSunLight();
  };
  //初始化场景
  initScene = () => {
    scene = new THREE.Scene();
    scene.background = new THREE.Color(0xf2f2f2);
    // scene.fog = new THREE.Fog(0xf2f2f2, 200, 6000);
    // scene.background = loadTexture(bg);
  };
  //添加到场景中
  addScene = (object: any) => {
    scene.add(object);
  };
  //从场景中移除
  removeScene = (object: any) => {
    scene.remove(object);
  };
  //加载纹理
  loadTexture = (imgSrc: string) => {
    const loader = new THREE.TextureLoader();
    loader.setCrossOrigin("anonymous");
    const texture = loader.load(imgSrc);
    return texture;
  };
  animate = () => {
    window.cancelAnimationFrame(stopAnimation);
    stopAnimation = requestAnimationFrame(this.animate);
    this.renderThree();
    controls.update();
  };
  //执行渲染
  renderThree = () => {
    renderer.render(scene, camera);
  };
  // //设置背景色
  // function setClearColor() {
  //   renderer.setClearColor(new THREE.Color('#cccccc'));
  // }
  //图片转CANVAS贴图 ps:解决PNG不能透明问题
  createCanvas = () => {
    let width = de.width * 5;
    let height = de.height * 5;
    let scale = 1; //缩放比例

    return new Promise((resolve) => {
      let canvas: HTMLCanvasElement = document.createElement("canvas");
      let context: any = canvas.getContext("2d");
      let img = new Image();
      img.src = textureImg || texture_img; //底色
      img.setAttribute("crossOrigin", "Anonymous");
      canvas.width = width * scale;
      canvas.height = height * scale;

      context.fillStyle = "#ffffff";
      context.fillRect(0, 0, canvas.width, canvas.height);
      img.onload = function () {
        const ptrn = context.createPattern(img, "repeat");
        context.fillStyle = ptrn;
        context.fillRect(0, 0, canvas.width, canvas.height);

        const svgTag = document.createElementNS(
          "http://www.w3.org/2000/svg",
          "svg"
        );
        const svgEle = document.getElementById("lux-svg-root") as any;
        svgTag.append(svgEle.cloneNode(true));
        svgTag.setAttribute("xmlns", "http://www.w3.org/2000/svg");
        svgTag.setAttribute("xmlns:xlink", "http://www.w3.org/1999/xlink");
        let svgImg = new Image();
        svgImg.src =
          "data:image/svg+xml;base64," +
          window.btoa(unescape(encodeURIComponent(svgTag.outerHTML)));
        svgImg.setAttribute("crossOrigin", "Anonymous");
        svgImg.onload = function () {
          context.drawImage(this, 0, 0, width, height);
          // document.body.appendChild(canvas);
          resolve(canvas);
        };
      };
    });
  };
  //创建模型
  createBox = async () => {
    const canvas: any = await this.createCanvas();
    // const ThreeBSP = require('three-js-csg')(THREE);
    object = new THREE.Group(); // 外层对象
    object.name = "wrapGroup";

    //正面材质用CANVAS的
    canvasMaterial = new THREE.MeshLambertMaterial({
      color: sideColor,
      side: THREE.DoubleSide,
      map: new THREE.CanvasTexture(canvas),
    });

    //背面材质-一般是底色
    backMaterial = new THREE.MeshLambertMaterial({
      color: sideColor,
      map: this.loadTexture(texture_img),
    });
    //纹理材质
    skinMaterial = new THREE.MeshLambertMaterial({
      color: sideColor,
      map: this.loadTexture(skin_img),
    });
    this.createCube();
    scene.add(object);
    object.position.set(-totalWidth / 2, -totalHeight / 2, 0);
  };
  //绘制物体
  createCube = () => {
    cubeGroup = [];

    ge.p.forEach((element: any, index: number) => {
      const cubeWrap: any = new THREE.Object3D();
      cubeWrap.name = ge.b[index].split(":")[0];
      const nearName = ge.b[index].split(":")[1];
      cubeWrap.renderOrder = index + 1;

      const depth = de.z; //z轴

      let shape = new THREE.Shape();

      let xArr = [] as number[];
      let yArr = [] as number[];

      const len = element.a.length;
      for (let i = 0; i < len / 2; i++) {
        xArr.push(element.a[i * 2]);
        yArr.push(element.a[i * 2 + 1]);
        if (i === 0) {
          shape.moveTo(element.a[i * 2], element.a[i * 2 + 1]);
        } else {
          shape.lineTo(element.a[i * 2], element.a[i * 2 + 1]);
        }
      }

      let geometry = new THREE.ExtrudeGeometry(shape, {
        depth,
        bevelEnabled: false, //是否有斜角
        // bevelThickness: 1,
        // bevelSize: 1,
        // bevelOffset: 0,
        // bevelSegments: 1,
      });
      const maxX = (cubeWrap.maxX = Math.max.apply(null, xArr));
      const minX = (cubeWrap.minX = Math.min.apply(null, xArr));
      const maxY = (cubeWrap.maxY = Math.max.apply(null, yArr));
      const minY = (cubeWrap.minY = Math.min.apply(null, yArr));

      const width = maxX - minX;
      const height = maxY - minY;

      let hasTop, hasBottom, hasRight, hasLeft;

      const nearArr = [] as number[];
      const newXArr = [] as number[];
      const newYArr = [] as number[];

      //先找出当前面旁边面的名称做为索引
      ge.b.forEach((item: any) => {
        let itemSearch: number = item.search(cubeWrap.name);
        if (itemSearch != -1) {
          nearArr.push(
            itemSearch === 0 ? item.split(":")[1] : item.split(":")[0]
          );
        }
      });

      //找出旁边的面以后找出对应的P点

      nearArr.forEach((item) => {
        ge.b.forEach((item2: any, index2: number) => {
          if (item === item2.split(":")[0]) {
            const len = ge.p[index2].a.length;
            for (let i = 0; i < len / 2; i++) {
              newXArr.push(ge.p[index2].a[i * 2]);
              newYArr.push(ge.p[index2].a[i * 2 + 1]);
            }
          }
        });
      });

      hasLeft = newXArr.find((item) => item < minX);
      hasRight = newXArr.find((item) => item > maxX);
      hasBottom = newYArr.find((item) => item < minY);
      hasTop = newYArr.find((item) => item > maxY);

      if (
        cubeWrap.name.search("SG") !== -1 &&
        nearArr.join(";").search("SG") === -1
      ) {
        hasRight = undefined;
        hasLeft = undefined;
      }
      if (
        cubeWrap.name.search("XG") !== -1 &&
        nearArr.join(";").search("XG") === -1
      ) {
        hasRight = undefined;
        hasLeft = undefined;
      }

      let leftMaterial, rightMaterial, topMaterial, bottomMaterial;
      leftMaterial = hasLeft === undefined ? skinMaterial : canvasMaterial;
      rightMaterial = hasRight === undefined ? skinMaterial : canvasMaterial;
      topMaterial = hasTop === undefined ? skinMaterial : canvasMaterial;
      bottomMaterial = hasBottom === undefined ? skinMaterial : canvasMaterial;

      //重置并设置UV面
      geometry.faceVertexUvs[0] = [];

      //背面
      geometry.faceVertexUvs[0][0] = [
        new THREE.Vector2(0, 0),
        new THREE.Vector2(1, 0),
        new THREE.Vector2(1, 1),
      ];

      geometry.faceVertexUvs[0][1] = [
        new THREE.Vector2(1, 1),
        new THREE.Vector2(0, 1),
        new THREE.Vector2(0, 0),
      ];

      //正面
      geometry.faceVertexUvs[0][2] = [
        new THREE.Vector2(
          minX / totalWidth,
          1 - (totalHeight - maxY) / totalHeight
        ),
        new THREE.Vector2(minX / totalWidth, (maxY - height) / totalHeight),
        new THREE.Vector2(maxX / totalWidth, (maxY - height) / totalHeight),
        // new THREE.Vector2(0,1),
        // new THREE.Vector2(0,0),
        // new THREE.Vector2(1,0),
      ];

      geometry.faceVertexUvs[0][3] = [
        new THREE.Vector2(maxX / totalWidth, (maxY - height) / totalHeight),
        new THREE.Vector2(
          maxX / totalWidth,
          1 - (totalHeight - maxY) / totalHeight
        ),
        new THREE.Vector2(
          minX / totalWidth,
          1 - (totalHeight - maxY) / totalHeight
        ),
        // new THREE.Vector2(1,0),
        // new THREE.Vector2(1,1),
        // new THREE.Vector2(0,1),
      ];

      //右面
      geometry.faceVertexUvs[0][4] =
        hasRight === undefined
          ? [
              new THREE.Vector2(0.0001, 1),
              new THREE.Vector2(0, 1),
              new THREE.Vector2(0.0001, 1 - de.l / 10),
            ]
          : [
              new THREE.Vector2(
                maxX / totalWidth,
                (maxY - height) / totalHeight
              ),
              new THREE.Vector2(
                maxX / totalWidth,
                1 - (totalHeight - maxY) / totalHeight
              ),
              new THREE.Vector2(
                maxX / totalWidth,
                (maxY - height) / totalHeight
              ),
              // new THREE.Vector2(1,0),
              // new THREE.Vector2(1,1),
              // new THREE.Vector2(0,0),
            ];

      geometry.faceVertexUvs[0][5] =
        hasRight === undefined
          ? [
              new THREE.Vector2(0, 1),
              new THREE.Vector2(0, 1 - de.l / 10),
              new THREE.Vector2(0.0001, 1 - de.l / 10),
            ]
          : [
              new THREE.Vector2(
                maxX / totalWidth,
                1 - (totalHeight - maxY) / totalHeight
              ),
              new THREE.Vector2(
                maxX / totalWidth,
                1 - (totalHeight - maxY) / totalHeight
              ),
              new THREE.Vector2(
                maxX / totalWidth,
                (maxY - height) / totalHeight
              ),
              // new THREE.Vector2(1,1),
              // new THREE.Vector2(0,1),
              // new THREE.Vector2(0,0),
            ];

      //上面
      geometry.faceVertexUvs[0][6] =
        hasTop === undefined
          ? [
              new THREE.Vector2(Math.ceil(width / 11.45), 1),
              new THREE.Vector2(0, 1),
              new THREE.Vector2(Math.ceil(width / 11.45), 1 - de.l / 10),
            ]
          : [
              new THREE.Vector2(maxX / totalWidth, 0.0001),
              new THREE.Vector2(minX / totalWidth, 0.0001),
              new THREE.Vector2(maxX / totalWidth, 0),
              // new THREE.Vector2(1,1),
              // new THREE.Vector2(0,1),
              // new THREE.Vector2(1,0),
            ];
      geometry.faceVertexUvs[0][7] =
        hasTop === undefined
          ? [
              new THREE.Vector2(0, 1),
              new THREE.Vector2(0, 1 - de.l / 10),
              new THREE.Vector2(Math.ceil(width / 11.45), 1 - de.l / 10),
            ]
          : [
              new THREE.Vector2(minX / totalWidth, 0.0001),
              new THREE.Vector2(minX / totalWidth, 0),
              new THREE.Vector2(maxX / totalWidth, 0),
              // new THREE.Vector2(0,1),
              // new THREE.Vector2(0,0),
              // new THREE.Vector2(1,0),
            ];

      //左面
      geometry.faceVertexUvs[0][8] =
        hasLeft === undefined
          ? [
              new THREE.Vector2(0.0001, 1),
              new THREE.Vector2(0, 1),
              new THREE.Vector2(0.0001, 1 - de.l / 10),
              // new THREE.Vector2(0,1),
              // new THREE.Vector2(0,0),
              // new THREE.Vector2(1,1),
            ]
          : [
              new THREE.Vector2(
                minX / totalWidth,
                1 - (totalHeight - maxY) / totalHeight
              ),
              new THREE.Vector2(
                minX / totalWidth,
                (maxY - height) / totalHeight
              ),
              new THREE.Vector2(
                minX / totalWidth,
                1 - (totalHeight - maxY) / totalHeight
              ),
            ];

      geometry.faceVertexUvs[0][9] =
        hasLeft === undefined
          ? [
              new THREE.Vector2(0, 1),
              new THREE.Vector2(0, 1 - de.l / 10),
              new THREE.Vector2(0.0001, 1 - de.l / 10),
              // new THREE.Vector2(0,0),
              // new THREE.Vector2(1,0),
              // new THREE.Vector2(1,1),
            ]
          : [
              new THREE.Vector2(
                minX / totalWidth,
                (maxY - height) / totalHeight
              ),
              new THREE.Vector2(
                minX / totalWidth,
                (maxY - height) / totalHeight
              ),
              new THREE.Vector2(
                minX / totalWidth,
                1 - (totalHeight - maxY) / totalHeight
              ),
            ];

      //下面
      geometry.faceVertexUvs[0][10] =
        hasBottom === undefined
          ? [
              new THREE.Vector2(1, 1),
              new THREE.Vector2(Math.ceil(width / 11.45), 1),
              new THREE.Vector2(1, 1 - de.l / 10),
            ]
          : [
              new THREE.Vector2(0, 0),
              new THREE.Vector2(1, 0),
              new THREE.Vector2(0, 0.00001),
              // new THREE.Vector2(0,0),
              // new THREE.Vector2(1,0),
              // new THREE.Vector2(0,1),
            ];

      geometry.faceVertexUvs[0][11] =
        hasBottom === undefined
          ? [
              new THREE.Vector2(Math.ceil(width / 11.45), 1),
              new THREE.Vector2(Math.ceil(width / 11.45), 1 - de.l / 10),
              new THREE.Vector2(1, 1 - de.l / 10),
            ]
          : [
              new THREE.Vector2(1, 0),
              new THREE.Vector2(1, 0.00001),
              new THREE.Vector2(0, 0.00001),
              // new THREE.Vector2(1,0),
              // new THREE.Vector2(1,1),
              // new THREE.Vector2(0,1),
            ];

      const faceLen = geometry.faces.length;
      for (let i = 0; i < faceLen / 2; i++) {
        geometry.faces[i * 2].materialIndex = i;
        geometry.faces[i * 2 + 1].materialIndex = i;
      }

      geometry.computeFaceNormals();

      const mesh = new THREE.Mesh(geometry, [
        backMaterial, //后
        canvasMaterial, //前
        rightMaterial, //右
        topMaterial, //上
        leftMaterial, //左
        bottomMaterial, //下
      ]);

      cubeWrap.add(mesh);

      //设置材质属性平铺
      mesh.material.forEach((item) => {
        item.map.wrapS = item.map.wrapT = THREE.RepeatWrapping;
      });

      mesh.castShadow = true;

      //判断是否有邻近的面
      if (nearName) {
        cubeWrap.width = width;
        cubeWrap.height = height;

        (this.findCube(cubeGroup, nearName) as any).add(cubeWrap);

        //在起始点的左边
        const isXG = cubeWrap.name.search("XG") != -1; //是否是上盖
        const isSG = cubeWrap.name.search("SG") != -1; //是否是下盖
        if (minX < origin.minX && !isSG && !isXG) {
          cubeWrap.position.set(minX + width, 0, 0);
          mesh.position.set(-(minX + width), 0, 0);
          if (cubeWrap.parent.name === ge.b[0]) {
            cubeWrap.sideType = "left";
          } else {
            cubeWrap.position.set(-cubeWrap.parent.width, 0, 0);
          }
        }
        //在起始点的右边
        else if (minX > origin.minX && !isSG && !isXG) {
          cubeWrap.position.set(minX, 0, 0);
          mesh.position.set(-minX, 0, 0);
          if (cubeWrap.parent.name === ge.b[0]) {
            cubeWrap.sideType = "right";
          } else {
            cubeWrap.position.set(cubeWrap.parent.width, 0, 0);
          }
        }
        //非不动面的子集上方
        else if (
          minY > origin.minY &&
          isSG &&
          cubeWrap.parent.name !== ge.b[0]
        ) {
          cubeWrap.position.set(
            minX < origin.minX
              ? -cubeWrap.parent.maxX
              : -cubeWrap.parent.position.x,
            totalHeight - height,
            0
          );
          mesh.position.set(0, -(totalHeight - height), 0);
        }
        //不动面的子集正上方
        else if (
          minY > origin.minY &&
          isSG &&
          cubeWrap.parent.name === ge.b[0]
        ) {
          cubeWrap.position.set(0, totalHeight - height, 0);
          mesh.position.set(0, -(totalHeight - height), 0);
        }
        //非不动面的子集下方
        else if (
          minY < origin.minY &&
          isXG &&
          cubeWrap.parent.name !== ge.b[0]
        ) {
          cubeWrap.position.set(
            minX < origin.minX
              ? -cubeWrap.parent.maxX
              : -cubeWrap.parent.position.x,
            height,
            0
          );
          mesh.position.set(0, -height, 0);
        }
        //不动面的子集正下方
        else if (
          minY < origin.minY &&
          isXG &&
          cubeWrap.parent.name === ge.b[0]
        ) {
          cubeWrap.position.set(0, height, 0);
          mesh.position.set(0, -height, 0);
        }

        if (index === ge.p.length - 1) {
          object.add(cubeGroup[0]);
          this.initPlaneGeometry();
          setTimeout(() => {
            this.initAnimationAction();
          }, 1000);
        }
      } else {
        origin = { minX, minY, maxX, maxY };
        cubeWrap.position.z = depth / 2;
        cubeGroup[0] = cubeWrap;
      }
      mesh.position.z = -depth / 2;
    });
  };

  //递归递归在相邻的父级里插入子集
  findCube = (arr: Array<any>, name: string) => {
    let result;
    (arr || []).map((item: any) => {
      if (item.name === name) {
        result = item;
      } else {
        const child = this.findCube(item.children, name);
        if (child) result = child;
      }
    });
    return result;
  };

  //创建地面
  initPlaneGeometry = () => {
    ground = new THREE.Mesh(
      new THREE.PlaneBufferGeometry(10000, 10000),
      new THREE.MeshPhongMaterial({
        color: 0xffffff,
        depthWrite: false,
        transparent: true,
        opacity: 0.5,
      })
    );
    ground.rotation.x = -Math.PI / 2;
    ground.receiveShadow = true;
    ground.name = "ground";
    object.add(ground);

    grid = new THREE.GridHelper(10000, 100, 0xffffff, 0xffffff);
    grid.name = "grid";

    const firstPoint = ge.p[0].a;

    let xArr = [] as number[];
    let yArr = [] as number[];

    const len = firstPoint.length;
    for (let i = 0; i < len / 2; i++) {
      xArr.push(firstPoint[i * 2]);
      yArr.push(firstPoint[i * 2 + 1]);
    }

    const maxX = Math.max.apply(null, xArr);
    const minX = Math.min.apply(null, xArr);

    const firstWidth = maxX - minX;

    grid.position.x = origin.maxX - firstWidth / 2;
    ground.position.x = origin.maxX - firstWidth / 2;
    object.add(grid);
  };

  //滑块动画剪辑
    clipChange = (ev: any) => {
        const e = ev.target.value;
    AnimationAction.time = e / 100;
    AnimationAction.paused = true;
    this.setState({
      paused: !AnimationAction.paused,
      changeFlag: true,
    });
  }
  //播放or暂停
  switchPlayPause = () => {
    this.setState({
      changeFlag: false,
    });
    if (AnimationAction)
      AnimationAction.paused
        ? (AnimationAction.paused = false)
        : (AnimationAction.paused = true);

    this.setState({
      paused: !AnimationAction.paused,
    });
    if (
      AnimationAction.time === this.state.maxDuration &&
      !this.state.changeFlag
    )
      AnimationAction.time = null;
  }
  //注册动画
  initAnimationAction() {
    mixer = new THREE.AnimationMixer(object);
    const firstPoint = ge.p[0].a;

    let xArr = [] as number[];
    let yArr = [] as number[];

    const len = firstPoint.length;
    for (let i = 0; i < len / 2; i++) {
      xArr.push(firstPoint[i * 2]);
      yArr.push(firstPoint[i * 2 + 1]);
    }

    const maxX = Math.max.apply(null, xArr);
    const minX = Math.min.apply(null, xArr);
    const maxY = Math.max.apply(null, yArr);
    const minY = Math.min.apply(null, yArr);

    const width = maxX - minX;
    const height = maxY - minY;

    let KeyframeTrack = new THREE.KeyframeTrack(
      "wrapGroup.position[x]",
      [0, 3],
      [object.position.x, -(maxX - width / 2)]
    );

    const animationGroup = [
      KeyframeTrack,
      new THREE.KeyframeTrack(
        "wrapGroup.position[y]",
        [6, 9],
        [object.position.y, -(maxY - height / 2)]
      ),
      new THREE.KeyframeTrack(
        "wrapGroup.position[z]",
        [0, 3],
        [
          object.position.z,
          ge.b.join(",").search("SG") != -1 && ge.b.join(",").search("XG") != -1
            ? (de.height - height) / 2
            : de.height - height,
        ]
      ),
      new THREE.KeyframeTrack(
        "ground.position[y]",
        [6, 9],
        [
          ground.position.y,
          ge.b.join(",").search("SG") != -1 && ge.b.join(",").search("XG") != -1
            ? (de.height - height) / 2
            : de.height - height,
        ]
      ),
      new THREE.KeyframeTrack(
        "grid.position[y]",
        [6, 9],
        [
          grid.position.y,
          ge.b.join(",").search("SG") != -1 && ge.b.join(",").search("XG") != -1
            ? (de.height - height) / 2
            : de.height - height,
        ]
      ),
    ];

    ge.p.forEach((item: any, index: number) => {
      let xArr = [] as number[];
      let yArr = [] as number[];
      const len = item.a.length;
      for (let i = 0; i < len / 2; i++) {
        xArr.push(item.a[i * 2]);
        yArr.push(item.a[i * 2 + 1]);
      }

      // const maxX = Math.max.apply(null, xArr);
      const minX = Math.min.apply(null, xArr);
      // const maxY = Math.max.apply(null, yArr);
      // const minY = Math.min.apply(null, yArr);

      if (index > 0) {
        const axis =
          ge.b[index].search("SG") != -1 || ge.b[index].search("XG") != -1
            ? ".rotation[x]"
            : ".rotation[y]";
        let direction =
          minX < origin.minX
            ? [item.c[0], -Math.PI / (180 / item.c[1])]
            : [item.c[0], Math.PI / (180 / item.c[1])];

        if (ge.b[index].search("SG") != -1)
          direction = [item.c[0], -Math.PI / (180 / item.c[1])];
        if (ge.b[index].search("XG") != -1)
          direction = [item.c[0], Math.PI / (180 / item.c[1])];

        animationGroup.push(
          new THREE.KeyframeTrack(
            ge.b[index].split(":")[0] + axis,
            item.t,
            direction
          )
        );
      }
    });

    const duration = this.state.maxDuration;
    let right_clip = new THREE.AnimationClip(
      "Action",
      duration,
      animationGroup
    );
    AnimationAction = mixer.clipAction(right_clip); // 返回所传入的剪辑参数的AnimationAction
    AnimationAction.loop = THREE.LoopOnce; //循环播放状态
    AnimationAction.clampWhenFinished = true; //播放结束时冻结
    // AnimationAction.time = 11;
    AnimationAction.timeScale = 1; // 可以调节播放速度，默认是1。为0时动画暂停。值为负数时动画会反向执行。
    this.setState({
      paused: true,
    });
    clock = new THREE.Clock();
    this.animateRender();
    AnimationAction.play();
  }

  animateRender = () => {
    this.setState({
      time: AnimationAction.time * 100,
    });
      const threeSlider = document.querySelector('#three-slider');
      threeSlider?.setAttribute('value', (AnimationAction.time * 100).toString());
    if (AnimationAction.time === 0 && !this.state.changeFlag) {
      // AnimationAction.paused = false;
      this.setState({
        paused: !AnimationAction.paused,
      });

      // AnimationAction.setEffectiveTimeScale(1);
    } else if (
      AnimationAction.time === this.state.maxDuration &&
      !this.state.changeFlag
    ) {
      // AnimationAction.paused = false;
      this.setState({
        paused: !AnimationAction.paused,
      });
      // AnimationAction.setEffectiveTimeScale(-1);
    }
    stopAnimationAction = requestAnimationFrame(this.animateRender);
    mixer.update(clock.getDelta());
  };

  //释放内存
  dispose3d = () => {
    try {
      renderer.dispose();
      renderer.forceContextLoss();
      renderer.content = null;
      window.cancelAnimationFrame(stopAnimation);
      window.cancelAnimationFrame(stopAnimationAction);
      const threeDom = document.querySelector(".three-dom") as HTMLDivElement;
      threeDom.removeChild(renderer.domElement);
      renderer.domElement = null;
      renderer = null;
      AnimationAction = null;
    } catch (error) {}
  };

  //注册3D
  initThreePreview = () => {
    this.initScene();
    this.initPerspectiveCamera();
    this.initRender();
    this.initControls();
    this.createBox();
    this.initLight();
    this.animate();
  };

  componentDidMount() {
    const threeData = (window as any).threeData;
    ge = threeData.ge;
    de = threeData.de as any;
    totalWidth = threeData.de.width;
    totalHeight = threeData.de.height;
    this.setState({
      maxDuration: de?.duration,
    });
      
      this.initThreePreview();
      
      const slider = document.querySelector('#three-slider');
      slider?.addEventListener('change',this.clipChange)
      
  }

  componentWillUnmount() {
    this.dispose3d();
  }

  render = () => {
    return (
      <div id="three-dom">
        <div class="three-dom" ref="threeDom"></div>
        <div class="play-wrap">
          <x-slider
            id="three-slider"
            value="0"
            min="0"
            max="900"
            step="1"
          ></x-slider>
          <img
            onClick={() => this.switchPlayPause}
            style={{
              marginLeft: "10px",
              width: "13px",
              height: "16px",
              cursor: "pointer",
            }}
            src={this.state.paused ? pausePng : playPng}
          />
        </div>
      </div>
    );
  };
}

export default THREE_PREVIEW;
