import Draggable from "../dependencies/draggable";
import { Stage } from "..";
class Spline {
  #stage: Stage;
  #splineHud?: SVGElement;
  #secondaryNodeGrippies?: SVGElement;
  #helperLines?: SVGElement;
  #grippiesGroup?: SVGGElement;
  #bezierHud?: SVGElement;
  #bezierG?: SVGElement;
  #bezierP?: SVGPathElement;
  #bezierL1?: SVGLineElement;
  #bezierL2?: SVGLineElement;
  #bezierC1?: SVGCircleElement;
  #bezierC2?: SVGCircleElement;
  #paths = new Map();
  #grippies = new Map();
  #controls = new Map();
  #isDragging: Boolean = false;
  #isGrippieMoving: Boolean = false;
  #isDrawing: Boolean = false;
  #firstX = 0;
  #firstY = 0;
  #checkedGrippie?: SVGElement;
  #focusGrippie?: SVGElement;
  #startX = 0;
  #startY = 0;
  #sClientX = 0;
  #sClientY = 0;
  #isDisabled: Boolean = true;
  #times = 0;
  constructor(stage: Stage) {
    if (!stage) throw new Error("stage not found!");
    this.#stage = stage;
    this.#install();
  }
  #install = () => {
    this.#stage.stage?.addEventListener("mousedown", this.#mousedown);
    document.addEventListener("mousemove", this.#mousemove);
    document.addEventListener("mouseup", this.#mouseup);
    this.#splineHud = document.querySelector("#spline-hud") as SVGElement;
    this.#grippiesGroup = this.#splineHud?.querySelector(
      "[uid='primary-node-grippies']"
    ) as SVGGElement;
    this.#secondaryNodeGrippies = document.querySelector(
      "[uid='secondary-node-grippies']"
    ) as SVGElement;
    this.#helperLines = document.querySelector(
      "[uid='helper-lines']"
    ) as SVGElement;
    this.#bezierHud = document.querySelector(
      "#cubic-bezier-seg-hud"
    ) as SVGElement;
    this.#bezierG = this.#bezierHud.querySelector(
      "[uid='container']"
    ) as SVGElement;
    this.#bezierP = this.#bezierHud?.querySelector(
      "[uid='outline']"
    ) as SVGPathElement;
    this.#bezierL1 = this.#bezierHud.querySelector(
      "[uid='control-line-1']"
    ) as SVGLineElement;
    this.#bezierL2 = this.#bezierHud.querySelector(
      "[uid='control-line-2']"
    ) as SVGLineElement;
    this.#bezierC1 = this.#bezierHud.querySelector(
      "[uid='control-point-1-grippie']"
    ) as SVGCircleElement;
    this.#bezierC2 = this.#bezierHud.querySelector(
      "[uid='control-point-2-grippie']"
    ) as SVGCircleElement;
    this.#watch();
  };
  #watch = () => {
    this.#stage.watch(
      this.#stage.actions().UPDATED,
      (record: MutationRecord[]) => {
        if (this.#isDisabled) return;
        record.forEach((record: MutationRecord) => {
          this.#addNodes(record);
          this.#removeNodes(record);
        });
      }
    );

    this.#stage.history?.watch((operation: Symbol) => {
      if (
        this.#stage.currentType !== this.#stage.tools().SPLINE ||
        operation === this.#stage.history?.actions().ADD
      )
        return;
      this.#initGrippies();
    });
  };
  #addNodes = (record: MutationRecord) => {};
  #removeNodes = (record: MutationRecord) => {};
  #initGrippies = () => {
    (this.#grippiesGroup as any).innerHTML = "";
    const cCTM = (this.#stage.canvas as SVGAElement)?.getCTM() as SVGMatrix;
    const fragment = document.createDocumentFragment();
    this.#stage.select?.checkedsPaths().forEach((node: any) => {
      const d = node.getAttribute("d");
      const hasZ = d.toLowerCase().includes("z");
      const path = node as SVGPathElement;
      const pData = path.getPathData();
      const CTM = node.getCTM() as SVGMatrix;
      const sx = CTM.a / cCTM.a;
      const sy = CTM.d / cCTM.d;
      const dx = (CTM.e - cCTM.e) / cCTM.a;
      const dy = (CTM.f - cCTM.f) / cCTM.d;

      pData.forEach((item: any, index: number) => {
        if (item.type.toLowerCase() === "z") return;
        const x =
          item.type.toLowerCase() === "c" ? item.values[4] : item.values[0];
        const y =
          item.type.toLowerCase() === "c" ? item.values[5] : item.values[1];
        const cx = x + dx - (1 - sx) * x;
        const cy = y + dy - (1 - sy) * y;
        let pos = "mid";
        if (hasZ) {
          if (index > 0)
            fragment.append(this.#createGrippies(cx, cy, pos, path, index));
        } else {
          pos =
            index === 0 ? "start" : index === pData.length - 1 ? "end" : "mid";
          fragment.append(this.#createGrippies(cx, cy, pos, path, index));
        }
      });
    });
    this.#grippiesGroup?.append(fragment);
  };
  #mousedown = (ev: MouseEvent) => {
    if (ev.button !== 0 || this.#isDisabled) return;
    ev.stopPropagation();
    this.#focusGrippie = void 0;
    const { offsetX, offsetY, clientX, clientY, target } = ev;
    const t = target as SVGElement;
    if (this.#grippies.has(t.parentNode))
      this.#focusGrippie = t.parentNode as SVGElement;

    const cCTM = (this.#stage.canvas as SVGAElement)?.getCTM() as SVGMatrix;
    const x = (offsetX - cCTM.e) / cCTM.a;
    const y = (offsetY - cCTM.f) / cCTM.d;

    this.#isDragging = true;
    this.#startX = x;
    this.#startY = y;
    this.#sClientX = clientX;
    this.#sClientY = clientY;

    const isClickControl = t.classList.contains("secondary-node-grippie")
      ? (this.#focusGrippie = t)
      : void 0;

    if (this.#times === 0 && !this.#focusGrippie) {
      this.#firstX = x;
      this.#firstY = y;
      this.#setBezierC1(x, y);
      this.#setBezierC2(x, y);
      this.#isDrawing = true;
      (this.#bezierG as any).style.visibility = "visible";
      (this.#helperLines as any).innerHTML = "";
      (this.#secondaryNodeGrippies as any).innerHTML = "";
    } else if (!this.#isDrawing) {
      if (isClickControl) {
        this.#BezierControlDrag(ev, t);
      } else if (this.#focusGrippie) {
        this.#grippieDrag(ev, this.#focusGrippie as SVGElement);
      }
    }
  };
  #mousemove = (ev: MouseEvent) => {
    if (!this.#isDrawing) return;
    const { clientX, clientY } = ev;
    const { left, top } = this.#stage.stage?.getBoundingClientRect() as DOMRect;
    const m = this.#stage.svgRoot?.getMatrixIterator();
    const x = (clientX - left - m.e) / m.a;
    const y = (clientY - top - m.f) / m.d;

    const bCx = this.#bezierC1?.getAttribute("cx");
    const bCy = this.#bezierC1?.getAttribute("cy");
    const bx = this.#startX;
    const by = this.#startY;
    const dx = x - bx;
    const dy = y - by;
    const x1 = this.#isDragging ? -dx + bx : x;
    const y1 = this.#isDragging ? -dy + by : y;
    const x2 = this.#isDragging ? bx : x;
    const y2 = this.#isDragging ? by : y;

    if (this.#times === 0) {
      this.#setBezierP(`M ${bCx} ${bCy} L ${x} ${y}`);
    } else {
      this.#setBezierP(
        `M ${bCx} ${bCy} C ${bCx} ${bCy} ${x1} ${y1} ${x2} ${y2}`
      );
      this.#setBezierL2(x1, y1, x2, y2);
      this.#setBezierC2(x1, y1);
    }
  };
  #mouseup = () => {
    if (!this.#isDragging || !this.#isDrawing) return;
    this.#isDragging = false;
    this.#times++;
    const pointNode = document.elementFromPoint(this.#sClientX, this.#sClientY);
    const endNode = this.#grippies.has(pointNode?.parentNode)
      ? (pointNode?.parentNode as SVGAElement)
      : void 0;
    const pos = endNode?.dataset.position;
    const path = this.#grippies.get(this.#checkedGrippie);
    const curPath = this.#grippies.get(pointNode?.parentNode);
    const samePath = path === curPath;

    if (
      (pos === "mid" || pos === "end") &&
      !this.#isGrippieMoving &&
      this.#times > 0
    ) {
      console.log("click");
      this.#times = 0;
      this.#closeBezierG();
      return;
    }

    if (this.#times === 2) {
      this.#stage.history?.add(this.#stage.history.actions().ADD, "钢笔");
      const node = this.#createSvgNode("path");
      node.setAttribute("fill", this.#stage.options.fill || "#d8d8d8");
      node.setAttribute("stroke", this.#stage.options.stroke || "#000000");

      const d = this.#bezierP?.getAttribute("d") as string;
      node.setAttribute("d", d);
      this.#stage.svgRoot?.append(node);
      this.#stage.select?.set(node, { node });

      const start = this.#createGrippies(
        this.#firstX || 0,
        this.#firstY || 0,
        "start",
        node,
        0
      );
      const end = this.#createGrippies(
        this.#startX || 0,
        this.#startY || 0,
        "end",
        node,
        1
      );
      this.#grippiesGroup?.append(start);
      this.#grippiesGroup?.append(end);
    } else if (this.#times > 2) {
      if (pos !== "start") {
        this.#stage.history?.add(this.#stage.history.actions().EDIT, "钢笔");
      } else if (pos === "start" && this.#times === 3) {
        this.#times = 0;
        this.#closeBezierG();
        return;
      }

      const d = path.getAttribute("d");
      const drawPath = this.#bezierP?.getPathData()[1];

      const cx = Number(pointNode?.getAttribute("cx"));
      const cy = Number(pointNode?.getAttribute("cy"));

      const endCx = pos === "start" && samePath ? cx : this.#startX;
      const endCy = pos === "start" && samePath ? cy : this.#startY;
      const endPos = pos === "start" && samePath ? "mid" : "end";
      const pX = pos === "start" && samePath ? endCx : drawPath.values[4];
      const pY = pos === "start" && samePath ? endCy : drawPath.values[5];

      path.setAttribute(
        "d",
        `${d} ${drawPath.type} ${drawPath.values[0]} ${drawPath.values[1]} ${drawPath.values[2]} ${drawPath.values[3]} ${pX} ${pY}`
      );

      const end = this.#createGrippies(
        endCx,
        endCy,
        endPos,
        path,
        this.#times - 1
      );

      this.#grippiesGroup?.append(end);
      const preGrippie = this.#checkedGrippie?.previousSibling as SVGElement;
      if (preGrippie.dataset.position === "end") {
        preGrippie?.setAttribute("data-position", "mid");
        preGrippie?.querySelector(".inner-circle")?.remove();
      }
    }
    if (this.#times > 1) this.#setBezierC1(this.#startX, this.#startY);

    if (pos === "start" && samePath) {
      this.#stage.history?.add(this.#stage.history.actions().EDIT, "钢笔");
      const path = this.#grippies.get(endNode);
      const d = path.getAttribute("d");
      path.setAttribute("d", d + " Z");
      endNode?.remove();
      this.#times = 0;
      this.#closeBezierG();
      this.#isDrawing = false;
    }

    this.#stage.history?.end();
  };
  #grippieDrag = (ev: MouseEvent, node: Node) => {
    const draggable = new Draggable();
    const element = node as SVGElement;
    const oC = element.querySelector(".outer-circle");
    const iC = element.querySelector(".inner-circle");

    let startCx = 0;
    let startCy = 0;

    const path = this.#grippies.get(node);
    const pData = path.getPathData();
    const index = Number(element.getAttribute("index"));
    const cacheD = path.getAttribute("d");
    const hasZ = cacheD.toUpperCase().includes("Z");

    const firstPdataItem = pData[0];
    const secondPdataItem = pData[1];
    const curPdataItem = pData[index];
    const nextPdataItem =
      pData[index + 1]?.type.toUpperCase() === "Z"
        ? secondPdataItem
        : pData[index + 1];

    const lastPdataItem = hasZ
      ? pData[pData.length - 2]
      : pData[pData.length - 1];
    const cCTM = (this.#stage.canvas as SVGAElement)?.getCTM() as SVGMatrix;
    const CTM = path.getCTM() as SVGMatrix;
    const sx = CTM.a / cCTM.a;
    const sy = CTM.d / cCTM.d;
    const dx = (CTM.e - cCTM.e) / cCTM.a;
    const dy = (CTM.f - cCTM.f) / cCTM.d;

    draggable.install({
      element,
      start: () => {
        startCx = Number(oC?.getAttribute("cx"));
        startCy = Number(oC?.getAttribute("cy"));
      },
      move: (ev: Draggable) => {
        const { dataX, dataY } = ev;
        if (Math.abs(dataX) + Math.abs(dataY) > 2) {
          this.#isGrippieMoving = true;
          (this.#helperLines as any).innerHTML = "";
          (this.#secondaryNodeGrippies as any).innerHTML = "";
        }
        const moveX = startCx + dataX / cCTM.a;
        const moveY = startCy + dataY / cCTM.d;
        oC?.setAttribute("cx", moveX.toFixed(4));
        oC?.setAttribute("cy", moveY.toFixed(4));
        iC?.setAttribute("cx", moveX.toFixed(4));
        iC?.setAttribute("cy", moveY.toFixed(4));

        if (curPdataItem.type === "C") {
          const fV0 = firstPdataItem?.values[0];
          const fV1 = firstPdataItem?.values[1];
          const nV0 = nextPdataItem?.values[0];
          const nV1 = nextPdataItem?.values[1];
          const cV2 = curPdataItem.values[2];
          const cV3 = curPdataItem.values[3];
          const cV4 = curPdataItem.values[4];
          const cV5 = curPdataItem.values[5];

          const x2 = cV2 + dataX / cCTM.a;
          const y2 = cV3 + dataY / cCTM.d;
          const x = cV4 + dataX / cCTM.a;
          const y = cV5 + dataY / cCTM.d;

          const x1 = nV0 + dataX / cCTM.a;
          const y1 = nV1 + dataY / cCTM.d;

          const cx2 = cV2 + dx - (1 - sx) * cV2 + dataX / cCTM.a;
          const cy2 = cV3 + dy - (1 - sy) * cV3 + dataY / cCTM.d;

          const cx1 = nV0 + dx - (1 - sx) * nV0 + dataX / cCTM.a;
          const cy1 = nV1 + dy - (1 - sy) * nV1 + dataY / cCTM.d;

          const secondaryGrippie = this.#createSecondaryGrippies(cx2, cy2, 0);
          this.#controls.set(secondaryGrippie, {
            path,
            grippie: element,
          });

          if (nextPdataItem?.type === "C") {
            this.#createHelperLine(
              cx2,
              cy2,
              moveX,
              moveY,
              moveX,
              moveY,
              cx1,
              cy1
            );
            const secondaryGrippie = this.#createSecondaryGrippies(cx1, cy1, 1);
            this.#controls.set(secondaryGrippie, {
              path,
              grippie: element,
            });
            const nRep = `${cV2} ${cV3} ${cV4} ${cV5}`;
            const rep = `${x2} ${y2} ${x} ${y}`;
            // const reg = new RegExp("C.*" + nRep + "", "gim");
            path.setAttribute(
              "d",
              cacheD.replace(nRep, rep).replace(`${nV0} ${nV1}`, `${x1} ${y1}`)
            );
          } else {
            console.log(nextPdataItem);
            console.log(firstPdataItem);

            this.#createHelperLine(cx2, cy2, moveX, moveY);
            const nRep = `${cV2} ${cV3} ${cV4} ${cV5}`;
            const rep = `${x2} ${y2} ${x} ${y}`;
            const cXY = `${cV4} ${cV5}`;
            const sV0 = secondPdataItem.values[0];
            const sV1 = secondPdataItem.values[1];
            const sCX = sV0 + dataX / cCTM.a;
            const sCY = sV1 + dataY / cCTM.d;
            const secondCXY = `${sV0} ${sV1}`;

            const repM = new RegExp(
              "" + cXY + "(.*)" + secondCXY + "(.*)" + nRep + ""
            );

            this.#secondaryNodeGrippies
              ?.querySelectorAll("circle")?.[1]
              ?.remove();

            // if (
            //   curPdataItem !== lastPdataItem &&
            //   secondPdataItem.type !== "C"
            // ) {
            //   this.#secondaryNodeGrippies
            //     ?.querySelectorAll("circle")?.[1]
            //     ?.remove();
            // } else {
            //   const cx = sV0 + dx - (1 - sx) * sV0 + dataX / cCTM.a;
            //   const cy = sV1 + dy - (1 - sy) * sV1 + dataY / cCTM.d;

            //   const secondaryGrippie = this.#createSecondaryGrippies(cx, cy, 1);
            //   this.#controls.set(secondaryGrippie, {
            //     path,
            //     grippie: element,
            //   });
            // }

            if (hasZ) {
              // path.setAttribute(
              //   "d",
              //   cacheD.replace(repM, `${x} ${y} $1 ${sCX} ${sCY} $2 ${rep}`)
              // );
              path.setAttribute("d", cacheD.replace(nRep, rep));
            } else {
              path.setAttribute("d", cacheD.replace(nRep, rep));
            }
          }
        } else if (curPdataItem.type === "M" || curPdataItem.type === "L") {
          const cX1 = nextPdataItem?.values[0];
          const cY1 = nextPdataItem?.values[1];

          const cx = curPdataItem.values[0];
          const cy = curPdataItem.values[1];

          const x1 = nextPdataItem?.values[0] + dataX / cCTM.a;
          const y1 = nextPdataItem?.values[1] + dataY / cCTM.d;

          const x = curPdataItem.values[0] + dataX / cCTM.a;
          const y = curPdataItem.values[1] + dataY / cCTM.d;

          const nRep = `${cx} ${cy}`;
          const rep = `${x} ${y}`;

          path.setAttribute(
            "d",
            nextPdataItem?.type === "C"
              ? cacheD
                  .replace(nRep, rep)
                  .replace(`${cX1} ${cY1}`, `${x1} ${y1}`)
              : cacheD.replace(nRep, rep)
          );
        }

        (this.#stage.stage as any).style.cursor = "auto";
      },
      end: () => {
        this.#isGrippieMoving = false;
        draggable?.uninstall();
        (this.#stage.stage as any).style.cursor = "crosshair";
      },
    });
    draggable?.mousedown(ev);
  };
  #BezierControlDrag = (ev: MouseEvent, node: Node) => {
    const draggable = new Draggable();
    const element = node as SVGElement;
    const { path, grippie } = this.#controls.get(node);
    const cCTM = (this.#stage.canvas as SVGAElement)?.getCTM() as SVGMatrix;
    const bfd = path.getAttribute("d");

    const helperLine = this.#helperLines?.querySelector(
      "polyline"
    ) as SVGPolylineElement;

    const index = Number(grippie.getAttribute("index"));
    const pData = path.getPathData();
    const curPdataItem = pData[index];
    const nextPdataItem = pData[index + 1];

    let startCx = 0;
    let startCy = 0;

    draggable.install({
      element,
      start: () => {
        startCx = Number(element.getAttribute("cx"));
        startCy = Number(element.getAttribute("cy"));
      },
      move: (ev: Draggable) => {
        const { dataX, dataY } = ev;
        (this.#stage.stage as any).style.cursor = "auto";

        const x =
          element.dataset.index === "0"
            ? curPdataItem.values[2]
            : nextPdataItem.values[0];
        const y =
          element.dataset.index === "0"
            ? curPdataItem.values[3]
            : nextPdataItem.values[1];

        const moveX = (x + dataX / cCTM.a).toString();
        const moveY = (y + dataY / cCTM.d).toString();

        const cx = (startCx + dataX / cCTM.a).toString();
        const cy = (startCy + dataY / cCTM.d).toString();
        element?.setAttribute("cx", cx);
        element?.setAttribute("cy", cy);

        const nRep =
          element.dataset.index === "0"
            ? `${curPdataItem.values[2]} ${curPdataItem.values[3]}`
            : `${nextPdataItem.values[0]} ${nextPdataItem.values[1]}`;
        const rep = `${moveX} ${moveY}`;

        path.setAttribute("d", bfd.replace(nRep, rep));

        const svgpoint =
          helperLine.ownerSVGElement?.createSVGPoint() as DOMPoint;
        svgpoint.x = Number(cx);
        svgpoint.y = Number(cy);
        const index = element.dataset.index === "0" ? 0 : 3;

        helperLine.points.replaceItem(svgpoint, index);
      },
      end: () => {
        draggable?.uninstall();
        (this.#stage.stage as any).style.cursor = "crosshair";
      },
    });
    draggable?.mousedown(ev);
  };
  #createHelperLine = (
    x1: number,
    y1: number,
    x2: number,
    y2: number,
    x3?: number,
    y3?: number,
    x4?: number,
    y4?: number
  ) => {
    let helperLine = document.querySelector(
      ".helper-line"
    ) as SVGPolylineElement;

    if (!helperLine) {
      helperLine = this.#createSvgNode("polyline") as SVGPolylineElement;
      helperLine.classList.add("helper-line");
      this.#helperLines?.append(helperLine);
    }

    helperLine?.setAttribute(
      "points",
      `${x1} ${y1} ${x2} ${y2} ${x3 || ""} ${y3 || ""} ${x4 || ""} ${y4 || ""}`
    );
  };
  #createSecondaryGrippies = (cx: number, cy: number, index: number) => {
    const raiuds = (
      (this.#stage.options.secondaryGgrippieSize || 4) / this.#stage.matrix.a
    ).toFixed(4);

    let c = this.#secondaryNodeGrippies?.querySelectorAll("circle")?.[
      index
    ] as SVGCircleElement;

    if (!c) {
      c = this.#createSvgNode("circle") as SVGCircleElement;
      this.#secondaryNodeGrippies?.append(c);
      c.classList.add("secondary-node-grippie");
    }

    c.setAttribute("cx", cx.toString());
    c.setAttribute("cy", cy.toString());
    c.setAttribute("r", raiuds);
    c.setAttribute("data-index", index.toString());
    return c;
  };
  #setBezierP = (d: string) => {
    this.#bezierP?.setAttribute("d", d);
  };
  #setBezierC1 = (x: number, y: number) => {
    const raiuds =
      (this.#stage.options.bezierControlSize || 2.8) / this.#stage.matrix.a;
    this.#bezierC1?.setAttribute("cx", x.toFixed(9));
    this.#bezierC1?.setAttribute("cy", y.toFixed(9));
    this.#bezierC1?.setAttribute("r", raiuds.toFixed(9));
    this.#setBezierP(`M ${x} ${y}`);
  };
  #setBezierC2 = (x: number, y: number) => {
    const raiuds =
      (this.#stage.options.bezierControlSize || 2.8) / this.#stage.matrix.a;
    this.#bezierC2?.setAttribute("cx", x.toFixed(9));
    this.#bezierC2?.setAttribute("cy", y.toFixed(9));
    this.#bezierC2?.setAttribute("r", raiuds.toFixed(9));
  };
  #setBezierL1 = (x1: number, y1: number, x2: number, y2: number) => {
    this.#bezierL1?.setAttribute("x1", x1.toFixed(9));
    this.#bezierL1?.setAttribute("y1", y1.toFixed(9));
    this.#bezierL1?.setAttribute("x2", x2.toFixed(9));
    this.#bezierL1?.setAttribute("y2", y2.toFixed(9));
  };
  #setBezierL2 = (x1: number, y1: number, x2: number, y2: number) => {
    this.#bezierL2?.setAttribute("x1", x1.toFixed(9));
    this.#bezierL2?.setAttribute("y1", y1.toFixed(9));
    this.#bezierL2?.setAttribute("x2", x2.toFixed(9));
    this.#bezierL2?.setAttribute("y2", y2.toFixed(9));
  };
  #createGrippies = (
    x: number,
    y: number,
    pos: string,
    node: SVGElement,
    index: number
  ) => {
    // this.#unSelect();
    const g = this.#createSvgNode("g");
    g.classList.add("primary-node-grippie");
    g?.setAttribute("data-position", pos);

    // if (selected) g.toggleAttribute("data-selected");
    const oC = this.#createSvgNode("circle") as SVGCircleElement;
    oC.classList.add("outer-circle");

    const { a: scale } = this.#stage.matrix;
    const oSize = (this.#stage.options.outerCircleSize || 5) / scale;
    const iSize = (this.#stage.options.innerCircleSize || 1.5) / scale;

    oC.setAttribute("cx", x.toFixed(9));
    oC.setAttribute("cy", y.toFixed(9));
    oC.setAttribute("r", oSize.toFixed(9));
    g.append(oC);

    if (pos !== "mid") {
      const iC = this.#createSvgNode("circle") as SVGCircleElement;
      iC.classList.add("inner-circle");
      iC.setAttribute("cx", x.toFixed(9));
      iC.setAttribute("cy", y.toFixed(9));
      iC.setAttribute("r", iSize.toFixed(9));
      g.append(iC);
    }

    g.setAttribute("index", index.toString());
    this.#grippies.set(g, node);
    const points = this.#paths.get(node) || [];
    this.#paths.set(node, [...points, g]);
    this.#checkedGrippie = g;
    return g;
  };
  #unSelect = () => {
    const selecteds = document.querySelectorAll("[data-selected]");
    selecteds.forEach((node: Element) => {
      node.removeAttribute("data-selected");
    });
  };
  #createSvgNode(type: string): SVGElement {
    return document.createElementNS("http://www.w3.org/2000/svg", type);
  }
  #closeBezierG = () => {
    (this.#bezierG as any).style.visibility = "hidden";
    this.#setBezierC1(0, 0);
    this.#setBezierC2(0, 0);
    this.#setBezierL1(0, 0, 0, 0);
    this.#setBezierL2(0, 0, 0, 0);
  };
  #reset = () => {
    this.#closeBezierG();
    (this.#grippiesGroup as any).innerHTML = "";
    (this.#helperLines as any).innerHTML = "";
    (this.#secondaryNodeGrippies as any).innerHTML = "";
    // this.#secondaryNodeGrippies?.setHTML('');
    this.#paths.clear();
    this.#isDrawing = false;
    this.#startX = 0;
    this.#startY = 0;
    this.#firstX = 0;
    this.#firstY = 0;
    this.#times = 0;
  };
  disabled = (isDisabled: boolean) => {
    this.#isDisabled = isDisabled;
    if (isDisabled) {
      this.#reset();
    } else {
      this.#initGrippies();
    }
  };
  uninstall = () => {
    this.#stage.stage?.removeEventListener("mousedown", this.#mousedown);
    document.removeEventListener("mousemove", this.#mousemove);
    document.removeEventListener("mouseup", this.#mouseup);
    this.#reset();
  };
}

export default Spline;
